import {
  createContext,
  Dispatch,
  FC,
  PropsWithChildren,
  SetStateAction,
  useCallback,
  useContext,
  useMemo,
  useState,
} from 'react';

import { POITypes } from 'components/poi/api/poi-schemas';
import noop from 'lodash/noop';

import { ListingDistanceMatricesTypes } from '../api/listing-distance-matrices';
import { PlaceDetailsTypes } from '../api/place-details';

type Id = PlaceDetailsTypes['base']['id'];
type ById = Record<
  Id,
  PlaceDetailsTypes['base'] & ListingDistanceMatricesTypes['base'] & Pick<POITypes['base'], 'image'>
>;

export type PoiContextValue = {
  listingPoiIds: Id[];
  userPoiIds: Id[];
  poisById: ById;
  setPoisById: Dispatch<SetStateAction<ById>>;
  activeId: Id | null;
  setActiveId: Dispatch<SetStateAction<Id | null>>;
  clearActive: () => void;
  setUserPoiIds: Dispatch<SetStateAction<Id[]>>;
  setListingPoiIds: Dispatch<SetStateAction<Id[]>>;
};

export const PoiContext = createContext<PoiContextValue>({
  listingPoiIds: [],
  userPoiIds: [],
  poisById: {},
  activeId: null,
  setActiveId: noop,
  setPoisById: noop,
  clearActive: noop,
  setUserPoiIds: noop,
  setListingPoiIds: noop,
});

export const PoiContextProvider: FC<
  PropsWithChildren<Partial<Pick<PoiContextValue, 'poisById' | 'userPoiIds'>>>
> = (props) => {
  const { children, poisById: initialPoisById = {}, userPoiIds: initialUserPoiIds = [] } = props;

  const [activeId, setActiveId] = useState<PoiContextValue['activeId']>(null);
  const [poisById, setPoisById] = useState<PoiContextValue['poisById']>(initialPoisById);
  const [userPoiIds, setUserPoiIds] = useState<Id[]>(initialUserPoiIds);
  const [listingPoiIds, setListingPoiIds] = useState<Id[]>([]);

  const clearActive = useCallback(() => {
    setActiveId(null);
  }, []);

  return (
    <PoiContext.Provider
      value={useMemo(() => {
        return {
          poisById,
          activeId,
          setActiveId,
          setPoisById,
          clearActive,
          userPoiIds,
          listingPoiIds,
          setUserPoiIds,
          setListingPoiIds,
        };
      }, [activeId, clearActive, listingPoiIds, poisById, userPoiIds])}
    >
      {children}
    </PoiContext.Provider>
  );
};

export const usePoiContext = (): PoiContextValue => useContext(PoiContext);

export default PoiContext;
