import { addMonths } from 'date-fns';

import {
  getAvailabilityRangeFromDate,
  GetAvailabilityRangeFromDateParams,
  GetAvailabilityRangeFromDateReturn,
} from '../getAvailabilityRangeFromDate';

interface ShiftAvailabilityRangeParams
  extends Pick<GetAvailabilityRangeFromDateParams, 'minDate' | 'maxDate' | 'months'> {
  range: GetAvailabilityRangeFromDateReturn;
  shift: number;
}

type ShiftAvailabilityRangeReturn = GetAvailabilityRangeFromDateReturn;

export const shiftAvailabilityRange = (
  params: ShiftAvailabilityRangeParams,
): ShiftAvailabilityRangeReturn => {
  const { range, shift, maxDate, minDate, months } = params;

  const [from] = range;

  // if `shift` is negative, it will sub months
  const shiftedFrom = addMonths(from, shift);

  return getAvailabilityRangeFromDate({
    date: shiftedFrom,
    minDate,
    maxDate,
    months,
  });
};
