import { fetchClient } from '@packages/providers';
import { Identifier } from '@packages/types';
import { QueryFunction, useQueries, UseQueryOptions } from '@tanstack/react-query';
import { AVAILABILITY_RESOURCE, LISTINGS_RESOURCE } from 'application/resources';

import { listingAvailabilityResponseSchema } from './schema';
import { ListingAvailabilityResponseData } from './types';

export interface ListingAvailabilityRange {
  from: string;
  to: string;
}

export type ListingAvailabilityQueryOptions = Omit<
  UseQueryOptions<
    ListingAvailabilityResponseData,
    unknown,
    ListingAvailabilityResponseData,
    ListingAvailabilityQueryKey
  >,
  'queryKey' | 'queryFn'
>;

interface UseGetListingAvailabilitiesParams {
  listingId: Identifier | undefined;
  ranges: [ListingAvailabilityRange, ListingAvailabilityRange, ListingAvailabilityRange];
  options?: [
    ListingAvailabilityQueryOptions,
    ListingAvailabilityQueryOptions,
    ListingAvailabilityQueryOptions,
  ];
}

interface GetListingAvailabilityQueryKeyParams
  extends Pick<UseGetListingAvailabilitiesParams, 'listingId'> {
  range: ListingAvailabilityRange;
}

const getListingAvailabilityQueryKey = (params: GetListingAvailabilityQueryKeyParams) => {
  const { range, listingId } = params;
  return [LISTINGS_RESOURCE, AVAILABILITY_RESOURCE, listingId, range] as const;
};

type ListingAvailabilityQueryKey = ReturnType<typeof getListingAvailabilityQueryKey>;

const queryFn: QueryFunction<ListingAvailabilityResponseData, ListingAvailabilityQueryKey> = async (
  context,
) => {
  const { queryKey } = context;
  const [, , listingId, range] = queryKey;

  const {
    data: { data: listingAvailabilityResponseData },
  } = await fetchClient.get(
    {
      pathname: `${LISTINGS_RESOURCE}/${listingId}/${AVAILABILITY_RESOURCE}`,
    },
    {
      params: range,
    },
  );
  return listingAvailabilityResponseSchema.parse(listingAvailabilityResponseData);
};

export const useGetListingAvailabilities = (params: UseGetListingAvailabilitiesParams) => {
  const { listingId, ranges, options = [] } = params;

  const queries = ranges.map((range, i) => {
    const optionsItem = options[i];

    const isPassedEnabledFalse = optionsItem?.enabled === false;
    const enabled = isPassedEnabledFalse ? false : !!listingId;

    return {
      queryKey: getListingAvailabilityQueryKey({ listingId, range }),
      queryFn,
      ...optionsItem,
      enabled,
    };
  });

  type AvailabilityQuery = typeof queries[number];

  return useQueries({
    // We want queries to be tuple
    queries: queries as [AvailabilityQuery, AvailabilityQuery, AvailabilityQuery],
  });
};
