import { FC } from 'react';

import { getUrlWithTemplate } from '@packages/helpers';
import { DEFAULT_WEBSITE_NAME } from 'application/constants';
import { useLocaleContext } from 'contexts/LocaleContext';
import { useGetListing, useGetWebsite } from 'hooks/index';
import { get } from 'lodash';
import { NextSeoProvider } from 'providers/NextSeoProvider';

import { useOGDescription } from '../../hooks/useOGDescription';

export interface SeoProviderProps {
  url: string;
}

export const SeoProvider: FC<SeoProviderProps> = (props) => {
  const { url } = props;

  const { data: listing } = useGetListing();
  const { data: website } = useGetWebsite();
  const { locale } = useLocaleContext();

  const description = useOGDescription(listing);

  const firstImageSrc = get(listing, 'gallery.0.src');
  const listingTitle = get(listing, 'title');
  const company = get(website, 'company.name', DEFAULT_WEBSITE_NAME);

  if (!listing) return;

  return (
    <NextSeoProvider
      title={listingTitle}
      openGraph={{
        type: 'website',
        images: firstImageSrc
          ? [
              {
                url: getUrlWithTemplate({
                  url: firstImageSrc,
                  template: 'custom',
                  height: 630,
                }),
                alt: listingTitle,
              },
            ]
          : undefined,
        title: listingTitle,
        description,
        url,
        locale,
        siteName: company,
      }}
    />
  );
};
