import { FC, ReactNode } from 'react';

import { Box, BoxProps, Theme, useMediaQuery } from '@mui/material';
import { HEADER_HEIGHT, MOBILE_HEADER_HEIGHT } from 'theme/index';

interface AnchorSectionProps extends BoxProps {
  children: ReactNode;
  id: string;
}

export const AnchorSection: FC<AnchorSectionProps> = (props) => {
  const { children, id, sx, ...rest } = props;
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const headerHeight = isMobile ? MOBILE_HEADER_HEIGHT : HEADER_HEIGHT;

  const topOffset = `calc(${headerHeight}px + 16px)`;

  return (
    <Box id={id} sx={{ scrollMarginTop: topOffset, ...sx }} {...rest}>
      {children}
    </Box>
  );
};
