import { differenceInCalendarMonths, isAfter, isBefore } from 'date-fns';

import { GetAvailabilityRangeFromDateReturn } from '../getAvailabilityRangeFromDate';
import { shiftAvailabilityRange } from '../shiftAvailabilityRange';

export interface GetUpdatedQueryAvailabilityRangeParams {
  visibleRange: GetAvailabilityRangeFromDateReturn;
  queryRange: GetAvailabilityRangeFromDateReturn;
  months: number;
  minDate: Date | undefined;
  maxDate: Date | undefined;
}

export type GetUpdatedQueryAvailabilityRangeReturn = GetAvailabilityRangeFromDateReturn;

export const getUpdatedQueryAvailabilityRange = (
  params: GetUpdatedQueryAvailabilityRangeParams,
): GetUpdatedQueryAvailabilityRangeReturn => {
  const { visibleRange, queryRange, months, minDate, maxDate } = params;

  const prevQueryRange = shiftAvailabilityRange({
    range: queryRange,
    shift: -months,
    minDate,
    maxDate,
    months,
  });

  const nextQueryRange = shiftAvailabilityRange({
    range: queryRange,
    shift: months,
    minDate,
    maxDate,
    months,
  });

  const [mainFrom, mainTo] = queryRange;
  const [visibleFrom, visibleTo] = visibleRange;

  const isVisibleFromBeforeMainFrom = isBefore(visibleFrom, mainFrom);
  const isVisibleToAfterMainTo = isAfter(visibleTo, mainTo);

  const isMinDateBeforevisibleFrom = minDate ? isBefore(minDate, visibleFrom) : true;
  const isMaxDateAftervisibleTo = maxDate ? isAfter(maxDate, visibleTo) : true;

  const shouldUpdateToPrevRange = isMinDateBeforevisibleFrom && isVisibleFromBeforeMainFrom;
  const shouldUpdateToNextRange = isMaxDateAftervisibleTo && isVisibleToAfterMainTo;

  // We subtract 1 to get "months between", not just the difference
  const mainFromVisibleToDifference = differenceInCalendarMonths(mainFrom, visibleTo) - 1;
  const visibleFromMainToDifference = differenceInCalendarMonths(visibleFrom, mainTo) - 1;

  const isBeforePrev = mainFromVisibleToDifference >= months;
  const isAfterNext = visibleFromMainToDifference >= months;

  if (isBeforePrev) {
    const shift = -mainFromVisibleToDifference % months;

    const shiftedRange = shiftAvailabilityRange({
      range: visibleRange,
      shift,
      minDate,
      maxDate,
      months,
    });

    return shiftedRange;
  }

  if (isAfterNext) {
    const shift = visibleFromMainToDifference % months;
    const shiftedRange = shiftAvailabilityRange({
      range: visibleRange,
      shift,
      minDate,
      maxDate,
      months,
    });

    return shiftedRange;
  }

  if (shouldUpdateToPrevRange) {
    return prevQueryRange;
  }

  if (shouldUpdateToNextRange) {
    return nextQueryRange;
  }

  return queryRange;
};
