import { isSameDay } from 'date-fns';

import { AvailabilityData } from '../../types';

interface GetDateAvailabilityAttributesParams {
  date: Date;
  data: AvailabilityData;
}

type GetDateAvailabilityAttributesReturn = AvailabilityData[number] | null;

export const getDateAvailabilityAttributes = (
  params: GetDateAvailabilityAttributesParams,
): GetDateAvailabilityAttributesReturn => {
  const { date, data } = params;

  const attributes = data.find((item) => {
    return isSameDay(item.date, date);
  });

  return attributes ?? null;
};
