import { differenceInCalendarDays } from 'date-fns';
import { getMinMaxStayDatesRestrictions } from 'helpers/getMinMaxStayDatesRestrictions';
import {
  LISTING_AVAILABILITY_STATUSES,
  ListingAvailabilityResponseData,
} from 'hooks/queries/useGetListingAvailabilities';
import { max, min } from 'lodash';
import { DateRangePickerInputViewProps } from 'views/DateRangePickerInputView';

import { PickerDayProps } from '../../components/PickerDay';
import { SelectedRange } from '../../types';
import { getClosestDate } from '../getClosestDate';
import { getDateAvailabilityAttributes } from '../getDateAvailabilityAttributes';

export interface GetBookingDatesRestrictionsParams
  extends Pick<DateRangePickerInputViewProps, 'disabledDay' | 'minDate' | 'maxDate'> {
  selectedRange: SelectedRange;
  data: ListingAvailabilityResponseData;
}

export interface GetBookingDatesRestrictionsReturn {
  minDate: DateRangePickerInputViewProps['minDate'];
  maxDate: DateRangePickerInputViewProps['maxDate'];
  disabledDay: NonNullable<DateRangePickerInputViewProps['disabledDay']>;
  getDayAttributes: NonNullable<PickerDayProps['getDayAttributes']>;
}

export const getBookingDatesRestrictions = (
  params: GetBookingDatesRestrictionsParams,
): GetBookingDatesRestrictionsReturn => {
  const {
    data,
    selectedRange,
    minDate: defaultMinDate,
    maxDate: defaultMaxDate,
    disabledDay: defaultDisabledDay,
  } = params;

  const [checkin, checkout] = selectedRange;
  const isOnlyCheckinSelected = checkin && !checkout;
  const isFullRangeSelected = checkin && checkout;

  const checkinAttributes = checkin ? getDateAvailabilityAttributes({ date: checkin, data }) : null;
  const { min_nights: checkinMinNights = null, max_nights: checkinMaxNights = null } =
    checkinAttributes || {};

  if (isFullRangeSelected) {
    const disabledDay: GetBookingDatesRestrictionsReturn['disabledDay'] = (date) => {
      const disabledByDefault = defaultDisabledDay?.(date);

      if (disabledByDefault) return true;

      const dayAttributes = getDateAvailabilityAttributes({ date, data });

      if (!dayAttributes) return true;

      const { is_checkin_available: isCheckinAvailable } = dayAttributes;

      return !isCheckinAvailable;
    };

    const getDayAttributes: GetBookingDatesRestrictionsReturn['getDayAttributes'] = (date) => {
      const dayData = getDateAvailabilityAttributes({ date, data });

      const isDayBeforeCheckin = differenceInCalendarDays(date, checkin) < 0;
      const isDayAfterCheckout = differenceInCalendarDays(date, checkout) > 0;

      if (!dayData || isDayBeforeCheckin || isDayAfterCheckout) {
        return { minNights: null, checkoutOnly: false };
      }

      const {
        is_checkin_available: isCheckinAvailable,
        is_checkout_available: isCheckoutAvailable,
        min_nights: dayMinNights,
        status,
      } = dayData;

      const isAvailableStatus = status === LISTING_AVAILABILITY_STATUSES['available'];
      const isUnavailableStatus = status === LISTING_AVAILABILITY_STATUSES['unavailable'];

      const isNotAvailableForCheckin = isAvailableStatus && !isCheckinAvailable;
      const checkoutOnly = isUnavailableStatus && isCheckoutAvailable;

      if (isNotAvailableForCheckin) {
        return { minNights: dayMinNights, checkoutOnly };
      }

      return { minNights: null, checkoutOnly };
    };

    return { minDate: defaultMinDate, maxDate: defaultMaxDate, disabledDay, getDayAttributes };
  }

  if (isOnlyCheckinSelected) {
    const lastPossibleCheckout = getClosestDate({
      data,
      date: checkin,
      position: 'after',
      filter: ({ status, is_checkout_available }) =>
        status === LISTING_AVAILABILITY_STATUSES['unavailable'] && is_checkout_available,
    });

    const { minDate: minDateFromMinMaxStay, maxDate: maxDateFromMinMaxStay } =
      getMinMaxStayDatesRestrictions({
        selectedRange,
        minStay: checkinMinNights,
        maxStay: checkinMaxNights,
        minDate: defaultMinDate,
        maxDate: defaultMaxDate,
      });

    const minDate = max([minDateFromMinMaxStay, defaultMinDate]);
    const maxDate = min([maxDateFromMinMaxStay, lastPossibleCheckout ?? undefined, defaultMaxDate]);

    const disabledDay: GetBookingDatesRestrictionsReturn['disabledDay'] = (date) => {
      const disabledByDefault = defaultDisabledDay?.(date);

      if (disabledByDefault) return true;

      const dayAttributes = getDateAvailabilityAttributes({ date, data });

      if (!dayAttributes) return true;

      const { is_checkout_available: isCheckoutAvailable } = dayAttributes;

      return !isCheckoutAvailable;
    };

    const getDayAttributes: GetBookingDatesRestrictionsReturn['getDayAttributes'] = (date) => {
      const dayData = getDateAvailabilityAttributes({ date, data });

      const checkinDaysDifference = differenceInCalendarDays(date, checkin);
      const isDayBeforeCheckin = checkinDaysDifference < 0;
      const isDayAfterLastPossibleCheckout = lastPossibleCheckout
        ? differenceInCalendarDays(date, lastPossibleCheckout) > 0
        : false;

      if (!dayData || isDayBeforeCheckin || isDayAfterLastPossibleCheckout) {
        return { minNights: null, checkoutOnly: false };
      }

      const {
        is_checkout_available: isCheckoutAvailable,
        min_nights: dayMinNights,
        status,
      } = dayData;

      const isAvailableStatus = status === LISTING_AVAILABILITY_STATUSES['available'];

      const isNotAvailableForCheckout = isAvailableStatus && !isCheckoutAvailable;

      const isDaySatisfiesCheckinMinNights = checkinMinNights
        ? checkinDaysDifference >= checkinMinNights
        : true;

      if (!isDaySatisfiesCheckinMinNights) {
        return { minNights: checkinMinNights, checkoutOnly: false };
      }

      if (isNotAvailableForCheckout) {
        return { minNights: dayMinNights, checkoutOnly: false };
      }

      return { minNights: null, checkoutOnly: false };
    };

    return { minDate, maxDate, disabledDay, getDayAttributes };
  }

  const disabledDay: GetBookingDatesRestrictionsReturn['disabledDay'] = (date) => {
    const disabledByDefault = defaultDisabledDay?.(date);

    if (disabledByDefault) return true;

    const dayAttributes = getDateAvailabilityAttributes({ date, data });

    if (!dayAttributes) return true;

    const { is_checkin_available: isCheckinAvailable } = dayAttributes;

    return !isCheckinAvailable;
  };

  const getDayAttributes: GetBookingDatesRestrictionsReturn['getDayAttributes'] = (date) => {
    const dayData = getDateAvailabilityAttributes({ date, data });

    if (!dayData) {
      return { minNights: null, checkoutOnly: false };
    }

    const {
      is_checkin_available: isCheckinAvailable,
      is_checkout_available: isCheckoutAvailable,
      min_nights: dayMinNights,
      status,
    } = dayData;

    const isAvailableStatus = status === LISTING_AVAILABILITY_STATUSES['available'];
    const isUnavailableStatus = status === LISTING_AVAILABILITY_STATUSES['unavailable'];

    const isNotAvailableForCheckin = isAvailableStatus && !isCheckinAvailable;
    const checkoutOnly = isUnavailableStatus && isCheckoutAvailable;

    if (isNotAvailableForCheckin) {
      return { minNights: dayMinNights, checkoutOnly };
    }

    return { minNights: null, checkoutOnly };
  };

  return { minDate: defaultMinDate, maxDate: defaultMaxDate, disabledDay, getDayAttributes };
};
