import { FC } from 'react';

import { Box } from '@mui/material';

import { anchorMenuItems } from '../../../application/constants';
import { Link } from '../../links/Link';

export interface AnchorMenuProps {
  items?: Array<{
    label: string;
    href: string;
  }>;
}

export const AnchorMenu: FC<AnchorMenuProps> = (props) => {
  const { items = anchorMenuItems } = props;
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        gap: 5,
        bgcolor: 'background.default',
        px: 6,
        border: 1,
        borderColor: 'primary.border',
        borderRadius: 4,
        height: 64,
        alignItems: 'center',
        overflow: 'auto',
      }}
    >
      {items.map((item) => {
        const { label, href } = item;
        return (
          <Link
            key={label}
            t={label}
            href={href}
            variant="body2"
            color="text.light"
            sx={{ textDecoration: 'none', ':hover': { color: 'primary.main', transition: '0.3s' } }}
          />
        );
      })}
    </Box>
  );
};

export default AnchorMenu;
