import { FC, useState } from 'react';

import {
  Box,
  CardMedia,
  dialogContentClasses,
  generateUtilityClasses,
  ImageList,
  ImageListItem,
} from '@mui/material';
import { getUrlWithTemplate, useBoolean } from '@packages/helpers';
import { WithRootProps } from '@packages/types';
import { useTranslate } from 'hooks/index';
import { ModalBase, ModalBaseProps } from 'modals/ModalBase';
import { FileRecord } from 'types/index';

import { PhotoGalleryModalMain } from './components/PhotoGalleryModalMain';

import { styled } from '../../../theme';

export type PhotoGalleryModalProps = WithRootProps<
  ModalBaseProps,
  {
    images: FileRecord[];
  }
>;

const PREFIX = 'PhotoGalleryModal';

export const PhotoGalleryModalClasses = generateUtilityClasses(PREFIX, [
  'imageWrapper',
  'imageListItem',
]);

export const StyledPhotoGalleryModal = styled(ModalBase, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const mobileBreakpoint = theme.breakpoints.down('md');
  return {
    [`&& .${dialogContentClasses.root}`]: {
      padding: 0,
    },

    [`& .${PhotoGalleryModalClasses.imageWrapper}`]: {
      margin: '0 auto',
      height: '100%',
      maxWidth: '150vh',
      [mobileBreakpoint]: {
        maxWidth: '100%',
      },
    },

    [`& .${PhotoGalleryModalClasses.imageListItem}`]: {
      cursor: 'pointer',
      ':after': {
        content: '""',
        position: 'absolute',
        left: 0,
        top: 0,
        width: '100%',
        height: '100%',
        transition: theme.transitions.create('opacity', {
          duration: theme.transitions.duration.standard,
        }),
        backgroundColor: '#000',
        opacity: 0,
      },
      ':hover': {
        ':after': {
          opacity: 0.3,
        },
      },
    },
  };
});

export const PhotoGalleryModal: FC<PhotoGalleryModalProps> = (props) => {
  const { images, ...rest } = props;
  const {
    value: isMainModalOpen,
    setTrue: handleOpenMainModal,
    setFalse: handleCloseMainModal,
  } = useBoolean(false);
  const [initialSlide, setInitialSlide] = useState(0);

  const translate = useTranslate({ basePath: 'modals.photo_gallery' });

  const handleOpenModalAndSetInitialSlide = (index = 0) => {
    setInitialSlide(index);
    handleOpenMainModal();
  };

  return (
    <StyledPhotoGalleryModal title={translate('title')} fullScreen {...rest}>
      <Box className={PhotoGalleryModalClasses.imageWrapper}>
        <ImageList variant="masonry" cols={2} gap={8}>
          {images.map((item, itemIndex) => (
            <ImageListItem
              key={item.id}
              onClick={() => handleOpenModalAndSetInitialSlide(itemIndex)}
              className={PhotoGalleryModalClasses.imageListItem}
            >
              <CardMedia
                src={getUrlWithTemplate({ url: item.src, template: 'original' })}
                alt={item.title}
                component="img"
              />
            </ImageListItem>
          ))}
        </ImageList>
      </Box>
      <PhotoGalleryModalMain
        images={images}
        initialSlide={initialSlide}
        open={isMainModalOpen}
        onClose={handleCloseMainModal}
      />
    </StyledPhotoGalleryModal>
  );
};

export default PhotoGalleryModal;
