import { FC, useMemo } from 'react';

import { Box, useTheme } from '@mui/material';
import { IcoMoon } from 'custom/IcoMoon';

import { IconWithTitleView, IconWithTitleViewProps } from '../LabeledIconView';

export interface AmenityViewProps extends IconWithTitleViewProps {
  isMatched?: boolean;
}

export const AmenityView: FC<AmenityViewProps> = (props) => {
  const { title, isMatched = false, ...rest } = props;

  const theme = useTheme();

  const renderTitle = useMemo(() => {
    if (isMatched) {
      return (
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          {title} <IcoMoon icon="fire" color={theme.palette.error.main} sx={{ ml: 1 }} />
        </Box>
      );
    } else {
      return title;
    }
  }, [isMatched, theme.palette.error.main, title]);

  return <IconWithTitleView {...rest} title={renderTitle} />;
};
