import { FC } from 'react';

import { Box, dialogContentClasses, generateUtilityClasses } from '@mui/material';
import { NextImage } from '@packages/components';
import { ModalBase, ModalBaseProps } from 'modals/ModalBase';
import { styled } from 'theme/index';

import { GallerySwiper } from './GallerySwiper';

import { PhotoGalleryModalProps } from '../PhotoGalleryModal';

interface PhotoGalleryModalMainProps
  extends Pick<PhotoGalleryModalProps, 'images'>,
    ModalBaseProps {
  initialSlide?: number;
}

const PREFIX = 'PhotoGalleryModalMain';

export const PhotoGalleryModalMainClasses = generateUtilityClasses(PREFIX, ['image']);

export const StyledModalBase = styled(ModalBase, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const mobile = theme.breakpoints.down('md');

  return {
    [`&& .${dialogContentClasses.root}`]: {
      padding: 0,
    },
    [`& .${PhotoGalleryModalMainClasses.image}`]: {
      margin: '0 auto',
      borderRadius: 16,
      [mobile]: {
        objectFit: 'contain',
      },
    },
  };
});

export const PhotoGalleryModalMain: FC<PhotoGalleryModalMainProps> = (props) => {
  const { images, initialSlide = 0, open, onClose } = props;

  return (
    <StyledModalBase open={open} fullScreen onClose={onClose}>
      <GallerySwiper initialSlide={initialSlide}>
        {images.map((imageData) => {
          const { id, src, title } = imageData;

          return (
            <Box key={id} className="swiper-zoom-container" height="100%" width="100%">
              <NextImage
                className={PhotoGalleryModalMainClasses.image}
                src={src}
                alt={title}
                style={{ objectFit: 'contain' }}
                fill
              />
            </Box>
          );
        })}
      </GallerySwiper>
    </StyledModalBase>
  );
};

export default PhotoGalleryModalMain;
