import { FC } from 'react';

import { Box, Theme, Typography, useMediaQuery } from '@mui/material';
import {
  ACCOMMODATES_SOURCE,
  BASE_PRICE_SLUG,
  BATHROOMS_SOURCE,
  BEDROOMS_SOURCE,
  listingRoute,
  RATING_COUNT_SLUG,
  RATING_SLUG,
} from 'application/constants';
import getFilterValues from 'helpers/getFilterValues';
import useGetPriceInCurrentCurrency from 'helpers/useGetPriceInCurrentCurrency';
import { useTranslate } from 'hooks/index';
import { RecommendedListings } from 'hooks/queries/useGetRecommendedListings';
import { ListingItem } from 'views/ListingItem';
import { SectionSwiper } from 'views/SectionSwiper';

interface RecommendedListingsSectionProps {
  recommendedListings: RecommendedListings;
}

export const RecommendedListingsSection: FC<RecommendedListingsSectionProps> = (props) => {
  const { recommendedListings } = props;

  const translate = useTranslate({ basePath: 'pages.listing' });
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('md'));
  const getPrice = useGetPriceInCurrentCurrency();

  return (
    <Box>
      <Typography variant="h4">{translate('titles.you_may_also_like')}</Typography>
      <Box mt={3}>
        <SectionSwiper slidesPerView={isMobile ? 1 : 3}>
          {recommendedListings?.map((listingData) => {
            const {
              formatted_filters,
              id,
              slug,
              gallery,
              title,
              currency,
              address,
              average_price,
            } = listingData;

            const [accommodates, bedrooms, bathrooms, basePrice, rating, ratingCount] =
              getFilterValues({
                formattedFilters: formatted_filters,
                slugs: [
                  ACCOMMODATES_SOURCE,
                  BEDROOMS_SOURCE,
                  BATHROOMS_SOURCE,
                  BASE_PRICE_SLUG,
                  RATING_SLUG,
                  RATING_COUNT_SLUG,
                ],
              });

            const parsedBasePrice = basePrice?.value ? Number(basePrice?.value) : 0;
            const parsedAveragePrice = average_price ? Number(average_price) : 0;
            /**
             * We show 'average_price' when dates are selected or 'base-price' when not
             */
            const priceValue = getPrice({
              initialCurrencyCode: currency.code,
              amount: parsedAveragePrice ? parsedAveragePrice : parsedBasePrice,
            });

            const zip = undefined; // no BE data

            return (
              <ListingItem
                key={id}
                address={address}
                name={title}
                zip={zip}
                href={{
                  pathname: listingRoute.getPathname(slug),
                }}
                price={priceValue}
                pricePrefix="from"
                rating={rating?.value}
                reviewsCount={ratingCount?.value}
                amenitiesItems={[
                  {
                    id: '1',
                    label: translate.root('custom.guest'),
                    value: accommodates?.value,
                  },
                  {
                    id: '2',
                    label: bedrooms?.filterAttribute?.title,
                    value: bedrooms?.value,
                  },
                  {
                    id: '3',
                    label: bathrooms?.filterAttribute?.title,
                    value: bathrooms?.value,
                  },
                ]}
                items={(gallery || []).map((galleryItem) => {
                  const { id, src, title } = galleryItem;
                  return {
                    id,
                    url: src,
                    title,
                  };
                })}
              />
            );
          })}
        </SectionSwiper>
      </Box>
    </Box>
  );
};
