import {
  ComponentType,
  FC,
  ReactElement,
  ReactNode,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';

import { Box, Button, ButtonProps } from '@mui/material';
import { useTranslate } from 'hooks/index';

interface SlotProps {
  toggleButton?: Partial<ButtonProps> & { showMoreText?: string; showLessText?: string };
}

type Slots = {
  [Key in keyof SlotProps]?: ComponentType<NonNullable<SlotProps[Key]>> | null;
};

export interface ExpandableWrapperProps {
  maxHeight: number;
  children: ReactNode;
  slots?: Slots;
  slotProps?: SlotProps;
  // in case the component is controlled from the outside
  expanded?: boolean;
  onChange?: (expanded: boolean) => void;
  isBlur?: boolean;
}

export const ExpandableWrapper: FC<ExpandableWrapperProps> = (props): ReactElement => {
  const {
    maxHeight,
    children,
    expanded = false,
    onChange,
    slots,
    slotProps,
    isBlur = false,
  } = props;

  const translate = useTranslate({ basePath: 'buttons' });
  const [containerNode, setContainerNode] = useState<HTMLElement | null>(null);
  const [offsetHeight, setOffsetHeight] = useState<number | undefined>();

  const { toggleButton: ToggleButton = Button } = slots || {};
  const { toggleButton: toggleButtonProps } = slotProps || {};

  const {
    sx: toggleButtonSx,
    children: toggleButtonChildren,
    showMoreText = translate('show_more'),
    showLessText = translate('show_less'),
    ...restToggleButtonProps
  } = toggleButtonProps || {};

  const [isExpanded, setIsExpanded] = useState(expanded);

  const isOverflow = useMemo(
    () => !!offsetHeight && offsetHeight > maxHeight,
    [maxHeight, offsetHeight],
  );

  // update offsetHeight on screen resize
  useEffect(() => {
    const changeOffsetHeight = () => {
      setOffsetHeight(containerNode?.offsetHeight);
    };

    changeOffsetHeight();
    window.addEventListener('resize', changeOffsetHeight);

    return () => {
      window.removeEventListener('resize', changeOffsetHeight);
    };
  }, [containerNode?.offsetHeight]);

  useEffect(() => {
    setIsExpanded(expanded);
  }, [expanded]);

  const handleToggleClick = useCallback(() => {
    const newValue = isExpanded ? false : true;

    setIsExpanded(newValue);
    onChange?.(newValue);
  }, [isExpanded, onChange]);

  const defaultLabel = isExpanded ? showLessText : showMoreText;

  const content = <Box ref={setContainerNode}>{children}</Box>;

  const isBlurred = isBlur && isOverflow && !isExpanded;

  return (
    <Box>
      <Box position="relative" maxHeight={isExpanded ? 'initial' : maxHeight} overflow="hidden">
        {content}
        {isBlurred && (
          <Box
            sx={{
              position: 'absolute',
              bottom: 0,
              width: '100%',
              height: 28,
              background:
                'linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, rgba(255, 255, 255, 0.74) 41.67%, #FFF 100%)',
            }}
          />
        )}
      </Box>
      {isOverflow && ToggleButton && (
        <ToggleButton
          variant="text"
          sx={{ display: 'block', mx: 'auto', mt: 3, ...toggleButtonSx }}
          onClick={handleToggleClick}
          color="secondary"
          {...restToggleButtonProps}
        >
          {toggleButtonChildren ?? defaultLabel}
        </ToggleButton>
      )}
    </Box>
  );
};
