import { isBrowser } from '@packages/helpers';
import { listingRoute } from 'application/constants';
import { LISTINGS_RESOURCE } from 'application/resources';
import { FooterClasses } from 'components/layouts/Footer/Footer';
import { MainLayout } from 'components/layouts/MainLayout';
import { ListingPage, ListingPageSeoProvider } from 'components/pageTemplates/ListingPage';
import ListContextProvider from 'contexts/ListContext';
import { withServerSideProps } from 'helpers/withServerSideProps';
import { prefetchFilterAttributes, prefetchListing, useGetListing } from 'hooks/index';
import { prefetchCurrencies } from 'hooks/queries/useGetCurrencies';
import { prefetchLocales } from 'hooks/queries/useGetLocales';
import type { NextPage } from 'next';
import { GoogleMapsAPIProvider } from 'providers/GoogleMapsAPIProvider';
import { MOBILE_FIXED_BOTTOM_SECTION_HEIGHT, styled } from 'theme/index';

const PREFIX = 'ListingShow';

const StyledListingShow = styled('div', {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => ({
  [`&& .${FooterClasses.root}`]: {
    [theme.breakpoints.down('md')]: {
      marginBottom: MOBILE_FIXED_BOTTOM_SECTION_HEIGHT,
    },
  },
}));

const ListingShow: NextPage<{ referer: string | null }> = (props) => {
  const { referer } = props;
  const { data: listing, isSuccess } = useGetListing();

  /* We use SSR 'referer' for initial render and change it with 'href' soon */
  const url = isBrowser ? window.location.href : referer ?? '';

  return (
    <GoogleMapsAPIProvider>
      <ListContextProvider>
        <ListingPageSeoProvider url={url} />
        <StyledListingShow>
          <MainLayout>
            {isSuccess && listing && <ListingPage data={listing} url={url} />}
          </MainLayout>
        </StyledListingShow>
      </ListContextProvider>
    </GoogleMapsAPIProvider>
  );
};

export default ListingShow;

export const getServerSideProps = withServerSideProps({
  enableRequestsOnClientSideTransition: true,
  fetchers: [prefetchListing, prefetchCurrencies, prefetchLocales, prefetchFilterAttributes],
  handler: (queryClient, context, props) => {
    const { req, query } = context;
    const listingIdOrSlug = query?.[listingRoute.param];
    const listing = queryClient.getQueryData([LISTINGS_RESOURCE, listingIdOrSlug]);

    if (!listing) {
      return {
        notFound: true,
      };
    }

    const { referer = null } = req.headers;

    return { props: { ...props, referer } };
  },
});
