import { FC } from 'react';

import { BookInfoForm, BookInfoFormProps } from 'custom/BookInfoForm';
import { ModalBase, ModalBaseProps } from 'modals/ModalBase';

export type BookInfoModalProps = ModalBaseProps & {
  formProps: BookInfoFormProps;
};

const BOOKING_MODAL_NAME = 'booking-modal';

export const BookInfoModal: FC<BookInfoModalProps> = (props) => {
  const { formProps } = props;
  return (
    <ModalBase {...props} urlName={BOOKING_MODAL_NAME}>
      <BookInfoForm {...formProps} />
    </ModalBase>
  );
};
