import { FC, useCallback, useMemo } from 'react';

import { MapPoint } from 'components/custom/Map/MapPoint';
import { MapPOIItem, MapPOIItemProps } from 'components/poi/views/MapPOIItem';
import { POIMapMarker, POIMapMarkerProps } from 'components/poi/views/POIMapMarker';
import { LatLngLiteral } from 'custom/Map';

export interface MapPoiPointProps extends Pick<POIMapMarkerProps, 'src'> {
  position: LatLngLiteral;
  selected?: boolean;
  onOpen: () => void;
  onClose: () => void;
  poiProps: MapPOIItemProps;
}

export const MapPoiPoint: FC<MapPoiPointProps> = (props) => {
  const { position, onOpen, selected = false, onClose, poiProps, src } = props;

  const handlePointClick = useCallback(() => {
    if (selected) {
      onClose();
      return;
    }
    onOpen();
  }, [selected, onOpen, onClose]);

  const listingCard = useMemo(() => <MapPOIItem {...poiProps} />, [poiProps]);

  return (
    <MapPoint
      position={position}
      card={listingCard}
      showCard={selected}
      point={<POIMapMarker src={src} selected={selected} onClick={handlePointClick} />}
    />
  );
};

export default MapPoiPoint;
