import { zodNumberSchema } from '@packages/providers';
import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

const base = z
  .object({
    walk_meters: zodNumberSchema,
    walk_seconds: zodNumberSchema,
    drive_meters: zodNumberSchema,
    drive_seconds: zodNumberSchema,
    bicycle_meters: zodNumberSchema,
    bicycle_seconds: zodNumberSchema,
  })
  .deepPartial();

export const zodListingDistanceMatricesSchemas = {
  base,
};

export type ListingDistanceMatricesTypes = TypesFromSchemas<
  typeof zodListingDistanceMatricesSchemas
>;
