import { FC } from 'react';

import { Box, Divider } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { useTranslate } from 'hooks/index';
import isEmpty from 'lodash/isEmpty';
import partition from 'lodash/partition';

import { Typography } from '../../Typography';
import {
  AmenitiesFieldView,
  AmenitiesFieldViewProps,
} from '../../views/AmenitiesFieldView/AmenitiesFieldView';

export interface AmenitiesSectionProps
  extends StyledRootProps,
    Pick<AmenitiesFieldViewProps, 'items'> {
  title?: string;
  withMatches?: boolean;
}

export const AmenitiesSection: FC<AmenitiesSectionProps> = (props) => {
  const { items, title, className, sx, withMatches = false } = props;

  const translate = useTranslate({ basePath: 'custom' });

  const isNoAmenities = isEmpty(items);

  const [matchedAmenities, anotherAmenities] = partition(items, ({ isMatched }) => isMatched);

  return (
    <Box component="section" className={className} sx={sx}>
      {title && (
        <Typography variant="h4" mb={4}>
          {title}
        </Typography>
      )}
      {isNoAmenities ? (
        <Typography variant="subtitle1">{translate('no_amenities')}</Typography>
      ) : (
        <>
          {withMatches ? (
            <>
              <AmenitiesFieldView
                title={translate('amenities', { context: 'matched' })}
                items={matchedAmenities}
                sx={{ mb: { xs: 0, md: 5 } }}
              />
              <Box sx={{ display: { xs: 'block', md: 'none' } }}>
                <Divider sx={{ my: 3 }} />
              </Box>
              <AmenitiesFieldView
                title={translate('amenities', { context: 'another' })}
                items={anotherAmenities}
              />
            </>
          ) : (
            <AmenitiesFieldView items={items} />
          )}
        </>
      )}
    </Box>
  );
};

export default AmenitiesSection;
