import { useCallback } from 'react';

import { getUrlWithTemplate, isBrowser } from '@packages/helpers';
import { useDeviceDetectContext } from '@packages/providers';
import { getAddressWithZip } from 'components/views/AddressView';
import { useSnackbar } from 'hooks/useSnackbar';
import { useTranslate } from 'hooks/useTranslate';

const getFileOrFail = (params: { url: string; fileName: string }) => {
  const { url, fileName } = params;

  const DEFAULT_WIDTH = 828;
  const width = isBrowser ? window.innerWidth : DEFAULT_WIDTH;
  const urlWithTemplate = getUrlWithTemplate({ url, width });
  return fetch(urlWithTemplate)
    .then((response) => response.blob())
    .then((blob) => {
      // Create a File object from the Blob
      const file = new File([blob], fileName, { type: blob.type });
      return file;
    });
};

export const useShareController = (params: {
  link: string;
  address: string;
  zip: string;
  description: string;
  imgSrc: string | null;
  imgName: string;
}) => {
  const { link, address, zip, description, imgSrc, imgName } = params;
  const { enqueueSnackbar } = useSnackbar();
  const { isMobile } = useDeviceDetectContext();

  const t = useTranslate();

  const MAX_DESCRIPTION_LENGTH = 50;
  const ELLIPSIS = '...';
  const shortDescription =
    description.length > MAX_DESCRIPTION_LENGTH
      ? description.slice(0, MAX_DESCRIPTION_LENGTH - ELLIPSIS.length) + ELLIPSIS
      : description;

  const isCopyButtonDisabled = !(typeof navigator !== 'undefined' && navigator.clipboard);
  const isShareButtonDisabled = !(typeof navigator !== 'undefined' && navigator.share);

  const shareTitle = t.root('custom.share_unit_title', {
    unit_nickname: getAddressWithZip({
      address,
      zip,
    }),
  });

  const handleCopy = useCallback(async () => {
    if (isCopyButtonDisabled) return;
    await navigator.clipboard.writeText(link);
    enqueueSnackbar(t.root('notification.copied'), { variant: 'info' });
  }, [isCopyButtonDisabled, link, t, enqueueSnackbar]);

  const handleShare = useCallback(async () => {
    if (navigator.share) {
      let file!: File | null | void;
      try {
        file = imgSrc ? await getFileOrFail({ url: imgSrc, fileName: imgName }) : null;

        navigator.share({
          title: shareTitle,
          text: shortDescription,
          url: link,
          files: file ? [file] : undefined,
        });
      } catch (err) {
        const error = err as Error;
        enqueueSnackbar(error.message, { variant: 'error' });
      }
    }
  }, [shareTitle, shortDescription, link, imgSrc, imgName, enqueueSnackbar]);

  return {
    handleShare: isMobile ? handleShare : handleCopy,
    disabled: isMobile ? isShareButtonDisabled : isCopyButtonDisabled,
  };
};

export default useShareController;
