import { CHECKIN_AT_SOURCE, CHECKOUT_AT_SOURCE } from 'application/constants';
import { bookingRangeFilterValueSchema, getNumericFilterValueSchema } from 'helpers/filters';
import { QUOTES_SOURCES } from 'hooks/queries/useGetQuotes';
import { z } from 'zod';

export const bookInfoFormValidationSchema = z.object({
  [QUOTES_SOURCES['checkin_at']]: bookingRangeFilterValueSchema
    .sourceType()
    .shape[CHECKIN_AT_SOURCE].or(z.literal('')),
  [QUOTES_SOURCES['checkout_at']]: bookingRangeFilterValueSchema
    .sourceType()
    .shape[CHECKOUT_AT_SOURCE].or(z.literal('')),
  [QUOTES_SOURCES['guest_count']]: getNumericFilterValueSchema().or(z.literal('')),
});
