import { zodCitySchemas, zodIdSchema, zodNumberSchema } from '@packages/providers';
import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

const zodPlaceDetailsSchema = z
  .object({
    id: zodIdSchema,
    address: z.string(),
    latitude: zodNumberSchema,
    longitude: zodNumberSchema,
  })
  .merge(
    z
      .object({
        api_id: zodIdSchema,
        city_id: zodIdSchema.or(z.null()),
        city: zodCitySchemas.base,
      })
      .deepPartial(),
  );

export const zodPlaceDetailsSchemas = {
  base: zodPlaceDetailsSchema,
};

export type PlaceDetailsTypes = TypesFromSchemas<typeof zodPlaceDetailsSchemas>;
