import { getFormattedDateRange } from 'helpers/getFormattedDateRange';
import { useTranslate } from 'hooks/index';

import { getDateAvailabilityAttributes } from '../../helpers/getDateAvailabilityAttributes';
import { AvailabilityData, SelectedRange } from '../../types';

interface UseGetSubtitleParams {
  selectedRange: SelectedRange;
  data: AvailabilityData;
}

type UseGetSubtitleReturn = string | undefined;

export const useGetSubtitle = (params: UseGetSubtitleParams): UseGetSubtitleReturn => {
  const { data, selectedRange } = params;

  const translate = useTranslate({ basePath: 'custom' });

  const [checkin, checkout] = selectedRange;
  const isFullRangeSelected = checkin && checkout;
  const isOnlyCheckinSelected = checkin && !checkout;

  if (isFullRangeSelected) {
    return getFormattedDateRange({
      startDate: checkin,
      endDate: checkout,
      dateDisplayFormat: 'd MMM yyyy',
    });
  }

  if (!isOnlyCheckinSelected) return;

  const checkinAttributes = getDateAvailabilityAttributes({ date: checkin, data });
  const { min_nights: checkinMinNights } = checkinAttributes || {};

  if (checkinMinNights) {
    return `${translate('minimum_stay')}: ${translate('night_count', { count: checkinMinNights })}`;
  }
};
