import { useEffect, useMemo, useRef, useState } from 'react';

import { Identifier, QueryMeta } from '@packages/types';
import { isAfter, isBefore } from 'date-fns';
import {
  ListingAvailabilityQueryOptions,
  ListingAvailabilityResponseData,
  useGetListingAvailabilities,
} from 'hooks/queries/useGetListingAvailabilities';
import { UseDateRangePickerInputProps } from 'inputs/DateRangePickerInput/hooks/useDateRangePickerInput';
import { isEqual } from 'lodash';
import { DateRangePickerInputViewProps } from 'views/DateRangePickerInputView';

import {
  getAvailabilityRangeFromDate,
  GetAvailabilityRangeFromDateReturn,
} from '../../helpers/getAvailabilityRangeFromDate';
import { getFormattedAvailabilityRange } from '../../helpers/getFormattedAvailabilityRange';
import { getUpdatedQueryAvailabilityRange } from '../../helpers/getUpdatedQueryAvailabilityRange';
import { shiftAvailabilityRange } from '../../helpers/shiftAvailabilityRange';

export interface UsePreloadedAvailabilityDataParams
  extends Pick<DateRangePickerInputViewProps, 'minDate' | 'maxDate'> {
  listingId?: Identifier;
  shownDate: Date;
  months: NonNullable<DateRangePickerInputViewProps['months']>;
  saveFormat: NonNullable<UseDateRangePickerInputProps['saveFormat']>;
}

export interface UsePreloadedAvailabilityDataReturn {
  data: ListingAvailabilityResponseData;
  loadingRanges: GetAvailabilityRangeFromDateReturn[];
}

export const usePreloadedAvailabilityData = (
  params: UsePreloadedAvailabilityDataParams,
): UsePreloadedAvailabilityDataReturn => {
  const { listingId, shownDate, months, minDate, maxDate, saveFormat } = params;

  const visibleAvailabilityRange = useMemo(
    () =>
      getAvailabilityRangeFromDate({
        date: shownDate,
        minDate,
        maxDate,
        months,
      }),
    [maxDate, minDate, months, shownDate],
  );

  const [mainQueryAvailabilityRange, setMainQueryAvailabilityRange] =
    useState<GetAvailabilityRangeFromDateReturn>(visibleAvailabilityRange);

  const prevQueryAvailabilityRange = useMemo(
    () =>
      shiftAvailabilityRange({
        range: mainQueryAvailabilityRange,
        shift: -months,
        minDate,
        maxDate,
        months,
      }),
    [minDate, maxDate, mainQueryAvailabilityRange, months],
  );

  const nextQueryAvailabilityRange = useMemo(
    () =>
      shiftAvailabilityRange({
        range: mainQueryAvailabilityRange,
        shift: months,
        minDate,
        maxDate,
        months,
      }),
    [minDate, maxDate, mainQueryAvailabilityRange, months],
  );

  const formattedPrevQueryAvailabilityRange = useMemo(
    () => getFormattedAvailabilityRange({ range: prevQueryAvailabilityRange, format: saveFormat }),
    [prevQueryAvailabilityRange, saveFormat],
  );

  const formattedMainQueryAvailabilityRange = useMemo(
    () => getFormattedAvailabilityRange({ range: mainQueryAvailabilityRange, format: saveFormat }),
    [mainQueryAvailabilityRange, saveFormat],
  );

  const formattedNextQueryAvailabilityRange = useMemo(
    () => getFormattedAvailabilityRange({ range: nextQueryAvailabilityRange, format: saveFormat }),
    [nextQueryAvailabilityRange, saveFormat],
  );

  const isPrevEnabled = minDate ? isBefore(minDate, mainQueryAvailabilityRange[0]) : true;
  const isNextEnabled = maxDate ? isAfter(maxDate, mainQueryAvailabilityRange[1]) : true;

  const listingAvailabilityQueryMeta: QueryMeta = {
    errorOptions: {
      errors: 'never',
      message: 'never',
    },
  };

  const commonAvailabilityQueriesOptions: ListingAvailabilityQueryOptions = {
    meta: listingAvailabilityQueryMeta,
  };

  const [
    { data: prevAvailabilityData, isFetching: isPrevAvailabilityDataFetching },
    { data: mainAvailabilityData, isFetching: isMainAvailabilityDataFetching },
    { data: nextAvailabilityData, isFetching: isNextAvailabilityDataFetching },
  ] = useGetListingAvailabilities({
    listingId,
    ranges: [
      formattedPrevQueryAvailabilityRange,
      formattedMainQueryAvailabilityRange,
      formattedNextQueryAvailabilityRange,
    ],
    options: [
      { ...commonAvailabilityQueriesOptions, enabled: isPrevEnabled },
      { ...commonAvailabilityQueriesOptions },
      { ...commonAvailabilityQueriesOptions, enabled: isNextEnabled },
    ],
  });

  const loadingRanges = useMemo(() => {
    const loadingRanges = [];

    if (isPrevAvailabilityDataFetching) {
      loadingRanges.push(prevQueryAvailabilityRange);
    }
    if (isMainAvailabilityDataFetching) {
      loadingRanges.push(mainQueryAvailabilityRange);
    }
    if (isNextAvailabilityDataFetching) {
      loadingRanges.push(nextQueryAvailabilityRange);
    }
    return loadingRanges;
  }, [
    isMainAvailabilityDataFetching,
    isNextAvailabilityDataFetching,
    isPrevAvailabilityDataFetching,
    mainQueryAvailabilityRange,
    nextQueryAvailabilityRange,
    prevQueryAvailabilityRange,
  ]);

  const availabilityData = useMemo(
    () => [
      ...(prevAvailabilityData || []),
      ...(mainAvailabilityData || []),
      ...(nextAvailabilityData || []),
    ],
    [mainAvailabilityData, nextAvailabilityData, prevAvailabilityData],
  );

  const prevVisibleAvailabilityRangeRef = useRef(visibleAvailabilityRange);

  useEffect(() => {
    const isVisibleAvailabilityRangeTheSame = isEqual(
      prevVisibleAvailabilityRangeRef.current,
      visibleAvailabilityRange,
    );

    if (isVisibleAvailabilityRangeTheSame) return;

    prevVisibleAvailabilityRangeRef.current = visibleAvailabilityRange;

    const updatedMainQueryAvailabilityRange = getUpdatedQueryAvailabilityRange({
      visibleRange: visibleAvailabilityRange,
      queryRange: mainQueryAvailabilityRange,
      minDate,
      maxDate,
      months,
    });

    setMainQueryAvailabilityRange(updatedMainQueryAvailabilityRange);
  }, [visibleAvailabilityRange, minDate, maxDate, mainQueryAvailabilityRange, months]);

  return {
    data: availabilityData,
    loadingRanges,
  };
};
