import { FC } from 'react';

import { Divider, Stack, StackProps, Typography, TypographyProps, useTheme } from '@mui/material';
import { IcoMoon, IcoMoonProps } from 'custom/IcoMoon';
import { useTranslate } from 'hooks/index';
import WithTitleTemplate from 'templates/WithTitleTemplate';

const defaultDivider = (
  <Divider sx={{ borderColor: 'text.primary' }} orientation="vertical" flexItem />
);

type TravelTypes = 'car' | 'bicycle' | 'walk' | 'transport';

export type DistancePOIStackProps = {
  travelMinutes: Partial<Record<TravelTypes, number>>;
  stackProps?: Partial<StackProps>;
  iconProps?: Partial<IcoMoonProps>;
  typographyProps?: Partial<TypographyProps>;
};

export const DistancePOIStack: FC<DistancePOIStackProps> = (props) => {
  const { travelMinutes, stackProps = {}, iconProps = {}, typographyProps = {} } = props;

  const { sx: typographyPropsSx = {} } = typographyProps;

  const theme = useTheme();
  const t = useTranslate({ basePath: 'features.poi' });

  const icons: Record<TravelTypes, string> = {
    car: 'private-car-outline',
    bicycle: 'bicycles-available',
    transport: 'public-transport-outline',
    walk: 'walking-man',
  };

  const orderedTravelTypes: TravelTypes[] = ['transport', 'car', 'bicycle', 'walk'];

  return (
    <Stack direction="row" divider={defaultDivider} spacing={1} {...stackProps}>
      {orderedTravelTypes.map((type) => {
        const durationInMinutes = Math.round(travelMinutes[type] || 0);

        if (!durationInMinutes) return null;
        const icon = icons[type as keyof typeof travelMinutes];

        const isValueExist = Boolean(durationInMinutes);

        const hours = Math.floor(durationInMinutes / 60);
        const minutes = durationInMinutes % 60;

        const formatedDuration = hours
          ? t.root('duration.hours_minutes', { hours, minutes })
          : t.root('duration.minutes', { minutes });
        return (
          <WithTitleTemplate
            key={type}
            space={0}
            title={
              <Typography
                variant="small1"
                color={isValueExist ? theme.palette.text.primary : theme.palette.text.disabled}
                sx={{ mr: isValueExist ? 0 : 2.5, ...typographyPropsSx }}
                {...typographyProps}
              >
                {isValueExist ? formatedDuration : '-'}
              </Typography>
            }
          >
            <IcoMoon
              size="extraSmall"
              color={isValueExist ? theme.palette.text.primary : theme.palette.text.disabled}
              icon={icon}
              sx={{ mr: 0.5 }}
              {...iconProps}
            />
          </WithTitleTemplate>
        );
      })}
    </Stack>
  );
};
