import { zodFileSchema, zodIdSchema, zodNumberSchema } from '@packages/providers';
import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

const zodPoiSchema = z
  .object({
    id: zodIdSchema,
    title: z.string(),
  })
  .merge(
    // partial
    z
      .object({
        latitude: zodNumberSchema,
        longitude: zodNumberSchema,
        walk_meters: zodNumberSchema,
        walk_seconds: zodNumberSchema,
        drive_meters: zodNumberSchema,
        drive_seconds: zodNumberSchema,
        bicycle_meters: zodNumberSchema,
        bicycle_seconds: zodNumberSchema,
        image: zodFileSchema,
      })
      .deepPartial(),
  );

/* ----------------------------------------------
                    Schemas
   ---------------------------------------------- */
export const zodPOISchemas = {
  base: zodPoiSchema,
  index: z.array(zodPoiSchema).default([]),
};

/* ----------------------------------------------
                    Types
   ---------------------------------------------- */
export type POITypes = TypesFromSchemas<typeof zodPOISchemas>;
