import { useCallback } from 'react';

import { Box, Skeleton, Stack, Typography, useMediaQuery, useTheme } from '@mui/material';
import { useBoolean } from '@packages/helpers';
import { fetchClient } from '@packages/providers';
import { Identifier, StyledRootProps } from '@packages/types';
import { useQueryClient } from '@tanstack/react-query';
import { getDistanceMatricesResource } from 'application/getResource';
import { PLACE_DETAILS_RESOURCES } from 'application/resources';
import { BlockWrapper } from 'components/views/BlockWrapper';
import { ScrollableWrapper } from 'components/views/ScrollableWrapper';
import { useTranslate } from 'hooks/index';

import { PlaceInput, PlaceInputProps } from './PlaceInput';
import { usePoiContext } from './PoiContext';

import { zodListingDistanceMatricesSchemas } from '../api/listing-distance-matrices';
import { zodPlaceDetailsSchemas } from '../api/place-details';
import { SearchPOIItem } from '../views/SearchPOIItem';

export interface MyPlacesProps extends StyledRootProps {
  listingId: Identifier;
}

export const MyPlaces = (props: MyPlacesProps) => {
  const { listingId, sx, className } = props;
  const { setFalse: unsetLoading, setTrue: setLoading, value: isLoading } = useBoolean(false);
  const { poisById, setPoisById, activeId, setActiveId, userPoiIds, setUserPoiIds } =
    usePoiContext();

  const theme = useTheme();
  const t = useTranslate({ basePath: 'features.poi.labels' });

  const queryClient = useQueryClient();
  const nonMobile = useMediaQuery(() => theme.breakpoints.up('sm'));

  const onDeletePlace = useCallback(
    (params: { id: Identifier }) => {
      const { id } = params;
      setUserPoiIds((prev) => {
        const placeIdsWithoutDeleted = prev.filter((v) => v !== id);

        return placeIdsWithoutDeleted;
      });
      setPoisById((prev) => {
        const { [id]: _, ...restPoisById } = prev;
        return restPoisById;
      });
    },
    [setPoisById, setUserPoiIds],
  );

  const onAddPlace = useCallback<NonNullable<PlaceInputProps['onAddPlace']>>(
    async (params) => {
      const { apiId } = params;
      setLoading();
      try {
        const placeDetails = await queryClient.fetchQuery({
          queryKey: [PLACE_DETAILS_RESOURCES, apiId],
          queryFn: async () => {
            const response = await fetchClient.get({
              pathname: PLACE_DETAILS_RESOURCES,
              api_id: apiId,
            });

            const parsedData = zodPlaceDetailsSchemas.base.parse(response.data?.data);

            return parsedData;
          },
        });

        const distanceMatricesPathname = getDistanceMatricesResource({ listingId });
        const distanceMatrices = await queryClient.fetchQuery({
          queryKey: [distanceMatricesPathname, placeDetails.id],
          queryFn: async () => {
            const response = await fetchClient.get({
              pathname: distanceMatricesPathname,
              poi_id: placeDetails.id,
            });

            return zodListingDistanceMatricesSchemas.base.parse(response.data?.data);
          },
        });

        setPoisById((prev) => {
          return { ...prev, [placeDetails.id]: { ...distanceMatrices, ...placeDetails } };
        });
        setUserPoiIds((prevIds) => {
          /* Move forward even if already selected */
          const placeIdsWithoutDeleted = prevIds.filter((v) => v !== placeDetails.id);
          return [placeDetails.id, ...placeIdsWithoutDeleted];
        });
      } finally {
        unsetLoading();
      }
    },
    [setLoading, queryClient, listingId, setPoisById, setUserPoiIds, unsetLoading],
  );

  const poiItemElements = userPoiIds.map((id) => {
    const poiDetails = poisById[id];
    if (!poiDetails) return null;
    const {
      address,
      walk_meters,
      bicycle_seconds = 0,
      drive_seconds = 0,
      walk_seconds = 0,
    } = poiDetails;

    return (
      <SearchPOIItem
        key={id}
        title={address}
        travelMinutes={{
          walk: walk_seconds / 60,
          bicycle: bicycle_seconds / 60,
          car: drive_seconds / 60,
        }}
        distanceMeters={walk_meters}
        onDeleteClick={() => onDeletePlace({ id })}
        src={''}
        onClick={() => {
          setActiveId((prevId) => (prevId === id ? null : id));
        }}
        selected={activeId === id}
      />
    );
  });

  const skeletonElement = !isLoading ? null : <Skeleton variant="rounded" height={114} />;

  const CARD_PADDING_SPACING = 2;
  const MOBILE_SLIDER_SPACING = 1;

  const mobileElement = (
    <ScrollableWrapper
      scroll="x"
      sx={{
        height: 'auto',
        mx: theme.spacing(-CARD_PADDING_SPACING),
        width: `calc(100% + ${theme.spacing(CARD_PADDING_SPACING * 2)})`,
      }}
      hiddenScrollbar
      scrollableBlockProps={{
        sx: {
          px: CARD_PADDING_SPACING,
        },
      }}
    >
      <Stack
        spacing={1}
        direction={'row'}
        sx={{
          '& > *': {
            flex: '0 0 auto',
          },
        }}
      >
        {skeletonElement}
        {poiItemElements}
        <Box
          /* To be able to scroll slider and to have some space at right. Padding does not help here */
          sx={{ width: theme.spacing(CARD_PADDING_SPACING - MOBILE_SLIDER_SPACING) }}
        />
      </Stack>
    </ScrollableWrapper>
  );

  const desktopElement = (
    <ScrollableWrapper sx={{ height: 'auto' }}>
      <Stack
        spacing={1.25}
        sx={{
          '& > *': {
            flex: '0 0 auto',
          },
        }}
      >
        {skeletonElement}
        {poiItemElements}
      </Stack>
    </ScrollableWrapper>
  );

  return (
    <BlockWrapper
      sx={{
        display: 'flex',
        flexDirection: 'column',
        maxWidth: 374,
        p: CARD_PADDING_SPACING,
        ...sx,
      }}
      className={className}
    >
      <Stack sx={{ minHeight: 0 }}>
        <Typography variant="h6" sx={{ mb: 1 }}>
          {t('my_places')}
        </Typography>
        <PlaceInput
          onAddPlace={onAddPlace}
          listingId={listingId}
          placeholder={t('search_your_favorite_places_and_distance_to_it')}
        />
        <Box
          sx={{
            mt: 0.5,
            minHeight: 0,
            display: 'flex',
          }}
        >
          {nonMobile ? desktopElement : mobileElement}
        </Box>
      </Stack>
    </BlockWrapper>
  );
};

export default MyPlaces;
