import { fetchClient } from '@packages/providers';
import { Identifier } from '@packages/types';
import { useQuery } from '@tanstack/react-query';
import { LISTINGS_RESOURCE } from 'application/resources';

import { POITypes, zodPOISchemas } from './poi-schemas';

interface FetchListingPOIParams {
  listingId: Identifier;
}

const getListingPOI = async (params: FetchListingPOIParams): Promise<POITypes['index']> => {
  const { listingId } = params;

  const {
    data: { data },
  } = await fetchClient.get({
    pathname: `${LISTINGS_RESOURCE}/${listingId}/poi`,
  });

  return zodPOISchemas['index'].parse(data);
};

export const useGetListingPOI = (params: { listingId: Identifier }) => {
  const { listingId } = params;
  return useQuery({
    queryKey: ['poi', listingId],
    queryFn: () => {
      return getListingPOI({ listingId });
    },
    refetchOnMount: false,
    enabled: !!listingId,
  });
};
