import { FC } from 'react';

import { Box, generateUtilityClasses, Typography, useTheme } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { useGetListingPOI } from 'components/poi';
import { MyPlaces, MyPlacesProps } from 'components/poi/components/MyPlaces';
import { ListingMapMarker } from 'custom/ListingMapMarker';
import { LatLngLiteral, Map, MapClasses, MapWrapper } from 'custom/Map';
import { useTranslate } from 'hooks/useTranslate';
import { styled } from 'theme/index';

import { MapPoiPoints } from './MapPoiPoints';

const PREFIX = 'ListingMap';

const ListingMapClasses = generateUtilityClasses(PREFIX, ['mapWrapper']);

const StyledListingMap = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(() => ({
  [`& .${ListingMapClasses.mapWrapper}`]: {
    height: 640,
  },
  [`& .${MapClasses.map}`]: {
    borderRadius: 16,
  },
}));

export interface ListingMapProps extends StyledRootProps, Pick<MyPlacesProps, 'listingId'> {
  location: LatLngLiteral;
}

export const ListingMap: FC<ListingMapProps> = (props) => {
  const { sx, className, location, listingId } = props;

  const theme = useTheme();
  const t = useTranslate({ basePath: 'pages.listing' });
  const { data: poiData = [] } = useGetListingPOI({ listingId });
  const isClosestPoiSectionHidden = !poiData.length;

  const myPlaces = (
    <MyPlaces
      listingId={listingId}
      sx={(() => {
        const WIDTH_PX = 374;
        return {
          flex: `1 1 ${WIDTH_PX}px`,
          maxWidth: '100%',
          [theme.breakpoints.up('sm')]: {
            maxWidth: WIDTH_PX,
          },
        };
      })()}
    />
  );

  return (
    <StyledListingMap sx={sx} className={className}>
      {isClosestPoiSectionHidden && (
        <Typography variant="h4" sx={{ mb: 4 }}>
          {t('titles.map_view')}
        </Typography>
      )}
      <MapWrapper className={ListingMapClasses.mapWrapper}>
        <Map center={location} zoom={15} header={myPlaces}>
          <ListingMapMarker position={location} />
          <MapPoiPoints />
        </Map>
      </MapWrapper>
    </StyledListingMap>
  );
};
