import { GetItemLayoutParams, GetItemLayoutReturn } from './types';

import { COLUMN_COUNT, ROW_COUNT } from '../../constants';

const getItemLayout = (params: GetItemLayoutParams): GetItemLayoutReturn => {
  const { isMobile, itemIndex, itemsCount } = params;

  if (isMobile) {
    return { cols: COLUMN_COUNT, rows: ROW_COUNT };
  }

  if (itemIndex === 0) {
    if (itemsCount === 1) {
      return { cols: COLUMN_COUNT, rows: ROW_COUNT };
    }
    if (itemsCount > 1) {
      return { cols: 3, rows: ROW_COUNT };
    }
  }

  if (itemIndex === 1) {
    if (itemsCount === 2) {
      return { cols: 2, rows: ROW_COUNT };
    }
    if (itemsCount >= 3 && itemsCount < 5) {
      return { cols: 2, rows: 1 };
    }
    if (itemsCount >= 5) {
      return { cols: 1, rows: 1 };
    }
  }

  if (itemIndex === 2) {
    if (itemsCount === 3) {
      return { cols: 2, rows: 1 };
    }
    if (itemsCount >= 4) {
      return { cols: 1, rows: 1 };
    }
  }

  if (itemIndex === 3) {
    return { cols: 1, rows: 1 };
  }

  return {};
};

export default getItemLayout;
