import { FC } from 'react';

import { Box, Grid } from '@mui/material';
import { StyledRootProps } from '@packages/types';

import { Typography } from '../../Typography';

export interface CancellationPolicySectionProps extends StyledRootProps {
  policyData: string[];
  title?: string;
}

export const CancellationPolicySection: FC<CancellationPolicySectionProps> = (props) => {
  const { policyData, sx, className, title = 'custom.cancellation_policy' } = props;

  return (
    <Box sx={sx} className={className}>
      <Typography variant="h4" sx={{ mb: 6 }}>
        {title}
      </Typography>
      <Grid container spacing={4}>
        {policyData.map((text) => {
          return (
            <Grid item md={12} key={text}>
              <Typography variant="subtitle1">{text}</Typography>
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};

export default CancellationPolicySection;
