import { FC } from 'react';

import { Box, generateUtilityClasses, useTheme } from '@mui/material';
import { POIMarkerIcon } from '@packages/assets';
import { NextImage } from '@packages/components';
import { getUrlWithTemplate } from '@packages/helpers';
import { IcoMoon } from 'custom/IcoMoon';
import { styled } from 'theme/index';

const PREFIX = 'POIMapMarker';

export const POIMapMarkerClasses = generateUtilityClasses(PREFIX, [
  'root',
  'outerIcon',
  'innerIcon',
  'innerImage',
]);

const IMAGE_SIZE = 44;

const StyledPOIMapMarker = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${POIMapMarkerClasses.root}`]: {
      position: 'relative',
      width: 60,
    },
    [`& .${POIMapMarkerClasses.outerIcon}`]: {
      fontSize: 60,
    },
    [`& .${POIMapMarkerClasses.innerImage}`]: {
      position: 'absolute',
      bottom: 0,
      left: '50%',
      transform: 'translate(-50%, -32%)',
      fontSize: theme.typography.pxToRem(28),
    },
    [`& .${POIMapMarkerClasses.innerIcon}`]: {
      position: 'absolute',
      bottom: 0,
      left: '50%',
      transform: 'translate(-50%, -32%)',
      borderRadius: '50%',
      background: theme.palette.background.paper,
      width: IMAGE_SIZE,
      height: IMAGE_SIZE,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
  };
});

export type POIMapMarkerProps = {
  src: string;
  selected: boolean;
  onClick: () => void;
};

export const POIMapMarker: FC<POIMapMarkerProps> = (props) => {
  const { src, selected, onClick } = props;
  const theme = useTheme();

  return (
    <StyledPOIMapMarker className={POIMapMarkerClasses.root} onClick={onClick}>
      <POIMarkerIcon
        className={POIMapMarkerClasses.outerIcon}
        fill={selected ? theme.palette.primary.main : theme.palette.primary.border}
      />

      {src ? (
        <NextImage
          src={getUrlWithTemplate({ url: src, width: 300 })}
          width={IMAGE_SIZE}
          height={IMAGE_SIZE}
          alt="Pin POI image"
          style={{ borderRadius: '50%' }}
          className={POIMapMarkerClasses.innerImage}
        />
      ) : null}
      {!src ? (
        <Box className={POIMapMarkerClasses.innerIcon}>
          <IcoMoon icon="apartment" color={theme.palette.text.light} />
        </Box>
      ) : null}
    </StyledPOIMapMarker>
  );
};
