import { ReactNode } from 'react';

import { Box, SxProps, useMediaQuery, useTheme } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { ITheme } from 'theme/index';

export interface BlockWrapperProps extends StyledRootProps {
  children?: ReactNode;
  variant?: 'primary' | 'secondary';
}

export function BlockWrapper(props: BlockWrapperProps) {
  const { children, sx, variant = 'primary', ...restProps } = props;

  const theme = useTheme<ITheme>();
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));

  const primarySxStyles: SxProps = {
    bgcolor: 'background.default',
    border: `1px solid ${theme.palette.primary.border}`,
    borderRadius: 4,
    ...(isDesktop
      ? { p: 5 }
      : {
          py: 7,
          px: 4,
        }),
  };

  const secondarySxStyles: SxProps = {
    p: 3,
    borderRadius: 4,
    border: `1px solid ${theme.palette.primary.border}`,
    background: theme.palette.primary.contrastText,
  };

  const wrapperSxStyles = variant === 'primary' ? primarySxStyles : secondarySxStyles;

  return (
    <Box sx={{ ...wrapperSxStyles, ...sx }} {...restProps}>
      {children}
    </Box>
  );
}

export default BlockWrapper;
