import { FC } from 'react';

import {
  Box,
  Button,
  generateUtilityClasses,
  ImageList,
  ImageListItem,
  useMediaQuery,
} from '@mui/material';
import { NextImage } from '@packages/components';
import { useBoolean } from '@packages/helpers';
import { StyledRootProps } from '@packages/types';
import { IconButton } from 'buttons/IconButton';
import { IcoMoon } from 'custom/IcoMoon';
import { useTranslate } from 'hooks/index';
import { styled, theme } from 'theme/index';
import { NoImageView } from 'views/NoImageView';

import { COLUMN_COUNT, PREVIEW_IMAGE_COUNT } from './constants';
import getItemLayout from './helpers/getItemLayout';

import {
  WithActionsWrapper,
  WithActionsWrapperClasses,
} from '../../layouts/WithActionsWrapper/WithActionsWrapper';
import { PhotoGalleryModal, PhotoGalleryModalProps } from '../../modals/PhotoGalleryModal';

export interface ListingGalleryViewProps extends StyledRootProps {
  images: PhotoGalleryModalProps['images'];
}

const PREFIX = 'ListingGallery';

export const ListingGalleryClasses = generateUtilityClasses(PREFIX, [
  'imageList',
  'emptyImage',
  'imageListItem',
  'actionButton',
]);

export const StyledListingGalleryView = styled(Box, {
  name: 'StyledListingGalleryView',
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const desktopQuery = theme.breakpoints.up('sm');
  const mobileQuery = theme.breakpoints.down('sm');
  return {
    width: '100%',
    height: 400,
    [desktopQuery]: {
      height: 472,
    },
    [`& .${ListingGalleryClasses.imageList}, .${ListingGalleryClasses.emptyImage}`]: {
      width: '100%',
      height: '100%',
      borderBottomLeftRadius: parseFloat(String(theme.shape.borderRadius)) * 4,
      borderBottomRightRadius: parseFloat(String(theme.shape.borderRadius)) * 4,
      [desktopQuery]: {
        borderRadius: 'unset',
      },
    },

    [`& .${ListingGalleryClasses.imageListItem}`]: {
      height: '100%',
      width: '100%',
      cursor: 'pointer',
      ':after': {
        content: '""',
        position: 'absolute',
        left: 0,
        top: 0,
        width: '100%',
        height: '100%',
        transition: theme.transitions.create('opacity', {
          duration: theme.transitions.duration.standard,
        }),
        backgroundColor: '#000',
        opacity: 0,
      },
      ':hover': {
        ':after': {
          opacity: 0.3,
        },
      },

      // without it borderRadius is not working
      position: 'relative',
      overflow: 'hidden',
      [desktopQuery]: {
        borderRadius: parseFloat(String(theme.shape.borderRadius)) * 2,
      },
    },

    [`& .${ListingGalleryClasses.emptyImage}`]: {
      [desktopQuery]: {
        borderRadius: parseFloat(String(theme.shape.borderRadius)) * 2,
      },
    },

    [`& .${WithActionsWrapperClasses.actions}`]: {
      zIndex: 1,
      [mobileQuery]: {
        top: 'auto',
        bottom: 0,
      },
    },

    [`& .${ListingGalleryClasses.actionButton}`]: {
      backgroundColor: theme.palette.background.default,
      color: theme.palette.text.primary,
    },
  };
});

export const ListingGalleryView: FC<ListingGalleryViewProps> = (props) => {
  const { images, ...rest } = props;

  const translate = useTranslate();

  const isDesktop = useMediaQuery(theme.breakpoints.up('sm'));

  const previewImagesCount = isDesktop ? PREVIEW_IMAGE_COUNT : 1;
  const previewImages = images.slice(0, previewImagesCount);
  const isGalleryImages = !!images.find((image) => image.src);
  const isOneImage = images.length === 1;

  const {
    value: isModalOpen,
    setTrue: handleModalOpen,
    setFalse: handleModalClose,
  } = useBoolean(false);

  const renderActionButton = isGalleryImages ? (
    !isDesktop ? (
      <>
        {!isOneImage ? (
          <IconButton
            variant="contained"
            onClick={handleModalOpen}
            sx={{ width: 16, height: 16 }}
            classes={{ contained: ListingGalleryClasses.actionButton }}
          >
            <IcoMoon icon="image" size="small" color={theme.palette.text.primary} />
          </IconButton>
        ) : null}
      </>
    ) : (
      <>
        {!isOneImage ? (
          <Button
            variant="contained"
            startIcon={<IcoMoon icon="image" color={theme.palette.text.primary} />}
            onClick={handleModalOpen}
            classes={{ root: ListingGalleryClasses.actionButton }}
          >
            {translate.root('buttons.show_all_items', {
              items: translate.root('custom.photo', { count: 2 }),
            })}
          </Button>
        ) : null}
      </>
    )
  ) : null;

  return (
    <StyledListingGalleryView {...rest}>
      {isGalleryImages && (
        <PhotoGalleryModal
          open={isModalOpen}
          urlName="listing-gallery"
          images={images.filter((image) => image.src)}
          onOpen={handleModalOpen}
          onClose={handleModalClose}
        />
      )}

      <WithActionsWrapper actions={renderActionButton} variant="topRight">
        {isGalleryImages ? (
          <ImageList
            className={ListingGalleryClasses.imageList}
            variant="quilted"
            cols={COLUMN_COUNT}
            gap={8}
            sx={{ marginBlockStart: 0 }}
          >
            {previewImages.map((imageData, itemIndex) => {
              const { id, src, title } = imageData;

              const layout = getItemLayout({
                itemIndex,
                isMobile: !isDesktop,
                itemsCount: previewImages.length,
              });

              return (
                <ImageListItem
                  onClick={handleModalOpen}
                  key={id}
                  className={ListingGalleryClasses.imageListItem}
                  {...layout}
                >
                  <NextImage src={src} alt={title} fill style={{ objectFit: 'cover' }} />
                </ImageListItem>
              );
            })}
          </ImageList>
        ) : (
          <NoImageView className={ListingGalleryClasses.emptyImage} />
        )}
      </WithActionsWrapper>
    </StyledListingGalleryView>
  );
};

export default ListingGalleryView;
