import { FC } from 'react';

import { Box, useTheme } from '@mui/material';
import { NextImage } from '@packages/components';
import { getUrlWithTemplate } from '@packages/helpers';
import { Typography } from 'components/Typography';
import { IcoMoon } from 'custom/IcoMoon';
import { useTranslate } from 'hooks/index';
import WithTitleTemplate from 'templates/WithTitleTemplate';
import { ITheme } from 'theme/index';

import { DistancePOIStack, DistancePOIStackProps } from '../DistancePOIStack';

export type DefaultPOIItemProps = {
  title: string;
  meters?: number;
  src?: string;
  isActive?: boolean;
  onClick: () => void;
} & Pick<DistancePOIStackProps, 'travelMinutes'>;

export const DefaultPOIItem: FC<DefaultPOIItemProps> = (props) => {
  const { src, title, meters, travelMinutes, isActive = false, onClick } = props;
  const theme = useTheme<ITheme>();

  const translate = useTranslate({ basePath: 'features.poi' });

  return (
    <Box sx={{ width: '100%' }}>
      <Box
        sx={{
          height: 136,
          position: 'relative',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          background: theme.palette.actions.background,
          borderRadius: 2,
          border: '1px solid',
          borderColor: isActive ? theme.palette.primary.main : theme.palette.primary.border,
          cursor: 'pointer',
          overflow: 'hidden',
        }}
        onClick={onClick}
      >
        {src ? (
          <NextImage
            src={getUrlWithTemplate({ url: src, width: 205, height: 136 })}
            fill
            alt="Default POI image"
            style={{ objectFit: 'cover', borderRadius: 8 }}
            unoptimized
          />
        ) : (
          <IcoMoon icon="apartment" size="large" color={theme.palette.text.light} />
        )}
      </Box>

      <Typography
        variant="subtitle2"
        sx={{ textOverflow: 'ellipsis', overflow: 'hidden', whiteSpace: 'nowrap' }}
        withTooltip
      >
        {title}
      </Typography>
      {meters ? (
        <WithTitleTemplate
          title={
            <Typography variant="small2">
              {translate('labels.distance_km', { km: Math.round((10 * meters) / 1000) / 10 })}
            </Typography>
          }
        >
          <IcoMoon icon="map-distance" size="small" color={theme.palette.primary.main} />
        </WithTitleTemplate>
      ) : null}

      <Box sx={{ mt: 0.5 }}>
        <DistancePOIStack travelMinutes={travelMinutes} />
      </Box>
    </Box>
  );
};
