import { useTranslate } from 'hooks/index';

import { DayAttributes } from './PickerDay';

type UseTooltipTitleParams = DayAttributes;

export const useTooltipTitle = (params: UseTooltipTitleParams): string | null => {
  const { minNights, checkoutOnly } = params;
  const translate = useTranslate({ basePath: 'custom' });

  if (checkoutOnly) {
    return translate('checkout_only');
  }
  if (minNights) {
    return translate('minimum_nights_count', { count: minNights });
  }
  return null;
};
