import { FC } from 'react';

import { Box, Button, generateUtilityClasses, IconButton, useTheme } from '@mui/material';
import { Typography } from 'components/Typography';
import { IcoMoon } from 'custom/IcoMoon';
import { useTranslate } from 'hooks/index';
import WithTitleTemplate from 'templates/WithTitleTemplate';
import { styled } from 'theme/index';

import { DistancePOIStack, DistancePOIStackProps } from '../DistancePOIStack';

const PREFIX = 'MapPOIItem';

export const MapPOIItemClasses = generateUtilityClasses(PREFIX, [
  'wrapper',
  'imageWrapper',
  'contentWrapper',
  'contentHeader',
]);

export const INSIDE_OFFSET_PX = 24;

const StyledMapPOIItem = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${MapPOIItemClasses.wrapper}`]: {
      maxWidth: 228,
      position: 'relative',
    },
    [`& .${MapPOIItemClasses.imageWrapper}`]: {
      height: 160,
      width: 228,
      position: 'relative',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      background: theme.palette.actions.background,
      borderRadius: 16,
    },
    [`& .${MapPOIItemClasses.contentWrapper}`]: {
      marginTop: theme.spacing(-INSIDE_OFFSET_PX / 8),
      background: theme.palette.background.default,
      borderRadius: 16,
      position: 'relative',
      padding: theme.spacing(2),
      border: `1px solid ${theme.palette.other.border}`,
    },
    [`& .${MapPOIItemClasses.contentHeader}`]: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
    },
  };
});

export type MapPOIItemProps = {
  title: string;
  distanceKm?: number;
  onShareButtonClick?: () => void;
  onCloseClick?: () => void;
} & Pick<DistancePOIStackProps, 'travelMinutes'>;

export const MapPOIItem: FC<MapPOIItemProps> = (props) => {
  const { title, distanceKm, travelMinutes, onShareButtonClick, onCloseClick } = props;

  const translate = useTranslate({ basePath: 'features.poi' });
  const theme = useTheme();

  return (
    <StyledMapPOIItem className={MapPOIItemClasses.wrapper}>
      {typeof onCloseClick !== 'undefined' ? (
        <Button
          variant="outlined"
          size="small"
          color="secondary"
          sx={{
            background: theme.palette.background.default,
            '&:hover': {
              background: theme.palette.background.default,
            },
            padding: theme.spacing(0.7),
            minWidth: 0,
            position: 'absolute',
            top: theme.spacing(1.5),
            right: theme.spacing(1.5),
            zIndex: 1,
          }}
          onClick={onCloseClick}
          aria-label={translate.root('buttons.close')}
        >
          <IcoMoon icon="close" size="small" color="inherit" />
        </Button>
      ) : null}
      <Box className={MapPOIItemClasses.contentWrapper}>
        <Box className={MapPOIItemClasses.contentHeader} sx={{ paddingRight: 4.5 }}>
          <Box sx={{ overflow: 'hidden' }}>
            <Typography
              variant="subtitle2"
              sx={{ textOverflow: 'ellipsis', overflow: 'hidden', whiteSpace: 'nowrap' }}
              withTooltip
            >
              {title}
            </Typography>
          </Box>

          {typeof onShareButtonClick !== 'undefined' ? (
            <IconButton onClick={onShareButtonClick}>
              <IcoMoon icon="show" color={theme.palette.text.light} />
            </IconButton>
          ) : null}
        </Box>

        {distanceKm ? (
          <WithTitleTemplate
            sx={{ my: 1 }}
            title={
              <Typography variant="small2">
                {translate('labels.distance_km', { km: Math.round(distanceKm * 10) / 10 })}
              </Typography>
            }
          >
            <IcoMoon icon="map-distance" size="small" color={theme.palette.primary.main} />
          </WithTitleTemplate>
        ) : null}

        <DistancePOIStack travelMinutes={travelMinutes} typographyProps={{ variant: 'small2' }} />
      </Box>
    </StyledMapPOIItem>
  );
};
