import { ReactElement, ReactNode } from 'react';

import { Box, BoxProps, generateUtilityClasses } from '@mui/material';
import { StyledRootProps } from '@packages/types';

const PREFIX = 'ScrollableWrapper';

export const scrollableWrapperClasses = generateUtilityClasses(PREFIX, ['scrollableBlock']);

export const ScrollableWrapper = (props: ScrollableWrapperProps): ReactElement => {
  const {
    children,
    scroll = 'xy',
    scrollableBlockProps = {},
    sx,
    hiddenScrollbar = false,
    ...rest
  } = props;

  return (
    <Box
      sx={{
        boxSizing: 'border-box',
        ...(() => {
          const distanceSpacing = 0.5;
          switch (scroll) {
            case 'x': {
              return {
                pb: distanceSpacing,
              };
            }
            case 'y': {
              return {
                pr: distanceSpacing,
              };
            }
            case 'xy': {
              return {
                pb: distanceSpacing,
                pr: distanceSpacing,
              };
            }
          }
        })(),

        height: '100%',
        width: '100%',
        ...sx,
      }}
      {...rest}
    >
      <Box
        className={scrollableWrapperClasses.scrollableBlock}
        {...scrollableBlockProps}
        sx={{
          boxSizing: 'border-box',

          ...(() => {
            const distanceSpacing = 0.5;

            if (hiddenScrollbar) {
              return {
                '&::-webkit-scrollbar': {
                  display: 'none' /* Hide scrollbar for Chrome, Safari and Opera */,
                },
              };
            }

            switch (scroll) {
              case 'x': {
                return {
                  pb: distanceSpacing,
                };
              }
              case 'y': {
                return {
                  pr: distanceSpacing,
                };
              }
              case 'xy': {
                return {
                  pb: distanceSpacing,
                  pr: distanceSpacing,
                };
              }
            }
          })(),
          overflow: 'auto',
          height: '100%',
          width: '100%',
          ...(scrollableBlockProps?.sx || {}),
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

export default ScrollableWrapper;

export interface ScrollableWrapperProps extends StyledRootProps {
  empty?: never;
  children: ReactNode;
  /**
   * scroll
   * Default: 'xy'
   * It controls extra padding: for 'x' - at the bottom, for 'y' - at the right, for 'xy' - both
   */
  scroll?: 'x' | 'y' | 'xy';
  scrollableBlockProps?: Partial<BoxProps>;
  hiddenScrollbar?: boolean;
}
