import { ListingTypes } from '@packages/providers';
import { PROPERTY_TYPE_SLUG, RATING_SLUG } from 'application/constants';
import { getValueFromFilter } from 'helpers/getValueFromFilter';
import { useTranslate } from 'hooks/index';
import { get } from 'lodash';

export const useOGDescription = (listing?: ListingTypes['show']): string => {
  const translate = useTranslate({ basePath: 'pages.listing.meta' });

  if (!listing) return '';

  const { value: rating } = getValueFromFilter({
    filtersArray: listing.formatted_filters,
    slug: RATING_SLUG,
  });

  const { value: propertyType } = getValueFromFilter({
    filtersArray: listing.formatted_filters,
    slug: PROPERTY_TYPE_SLUG,
  });

  const address = get(listing, 'address');
  const city = get(listing, 'city.title');

  const addressLine = translate('property_type_in_address_city', { propertyType, address, city });
  const ratingLine = rating ? ` ${rating}` : '';

  return `${addressLine}\n${ratingLine}`;
};
