import { FC } from 'react';

import { Box, generateUtilityClasses, useTheme } from '@mui/material';
import { MarkerIcon } from '@packages/assets';
import { OverlayView, OverlayViewF, OverlayViewProps } from '@react-google-maps/api';
import { IcoMoon } from 'custom/IcoMoon';
import { LatLngLiteral } from 'custom/Map';
import { styled } from 'theme/index';

export interface ListingMapMarkerProps {
  position: LatLngLiteral;
}

const getPixelPositionOffset: OverlayViewProps['getPixelPositionOffset'] = (width, height) => {
  return { x: -width / 2, y: -height };
};

const PREFIX = 'ListingMapMarker';

export const ListingItemClasses = generateUtilityClasses(PREFIX, [
  'root',
  'outerIcon',
  'innerIcon',
]);

const StyledRoot = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${ListingItemClasses.root}`]: {
      position: 'relative',
    },
    [`& .${ListingItemClasses.outerIcon}`]: {
      fontSize: 68,
    },
    [`& .${ListingItemClasses.innerIcon}`]: {
      position: 'absolute',
      bottom: 0,
      left: '50%',
      transform: 'translate(-50%, -100%)',
      fontSize: theme.typography.pxToRem(28),
    },
  };
});

export const ListingMapMarker: FC<ListingMapMarkerProps> = (props) => {
  const { position } = props;

  const theme = useTheme();

  return (
    <OverlayViewF
      position={position}
      mapPaneName={OverlayView.MARKER_LAYER}
      getPixelPositionOffset={getPixelPositionOffset}
    >
      <StyledRoot className={ListingItemClasses.root}>
        <MarkerIcon
          className={ListingItemClasses.outerIcon}
          htmlColor={theme.palette.background.default}
        />
        <IcoMoon
          icon="home"
          color={theme.palette.primary.main}
          className={ListingItemClasses.innerIcon}
        />
      </StyledRoot>
    </OverlayViewF>
  );
};
