import { FC, useCallback, useMemo, useState } from 'react';

import { getParsedToDateValue } from '@packages/helpers';
import { DEFAULT_SAVE_FORMAT } from 'inputs/DateRangePickerInput';
import { useDateRangePickerInput } from 'inputs/DateRangePickerInput/hooks/useDateRangePickerInput';
import { debounce } from 'lodash';
import DateRangePickerInputView from 'views/DateRangePickerInputView';

import { Picker as BookingDateRangePicker } from './components/Picker';
import { getBookingDatesRestrictions } from './helpers/getBookingDatesRestrictions';
import { useGetSubtitle } from './hooks/useGetSubtitle';
import { usePreloadedAvailabilityData } from './hooks/usePreloadedAvailabilityData';
import {
  BookingDateRangePickerInputProps,
  OnChange,
  OnShownDateChange,
  SelectedRange,
} from './types';

const DEFAULT_MIN_DATE = new Date();

export const BookingDateRangePickerInput: FC<BookingDateRangePickerInputProps> = (props) => {
  const {
    listingId,
    minDate = DEFAULT_MIN_DATE,
    maxDate,
    disabledDay,
    saveFormat = DEFAULT_SAVE_FORMAT,
    months = 2,
    ...rest
  } = props;

  const { fromName, toName } = rest;

  const { value, error, required, onAccept, onBlur } = useDateRangePickerInput({
    fromName,
    toName,
    saveFormat,
  });

  const [shownDate, setShownDate] = useState<Date>(value.startDate ?? value.endDate ?? new Date());

  const [selectedRange, setSelectedRange] = useState<SelectedRange>([
    getParsedToDateValue({ value: value.startDate, format: saveFormat }),
    getParsedToDateValue({ value: value.endDate, format: saveFormat }),
  ]);

  const { data: availabilityData, loadingRanges } = usePreloadedAvailabilityData({
    shownDate,
    months,
    saveFormat,
    listingId,
    minDate,
    maxDate,
  });

  const subtitle = useGetSubtitle({ selectedRange, data: availabilityData });

  const handleSelectedRangeChange = useCallback<OnChange>(({ startDate, endDate }) => {
    setSelectedRange([startDate, endDate]);
  }, []);

  const handleShownDateChange = useCallback<OnShownDateChange>((date) => {
    setShownDate(date);
  }, []);

  const debouncedShownDateChangeHandler = useMemo(
    () => debounce(handleShownDateChange, 250),
    [handleShownDateChange],
  );

  const {
    minDate: restrictedMinDate,
    maxDate: restrictedMaxDate,
    disabledDay: restrictedDisabledDay,
    getDayAttributes,
  } = useMemo(
    () =>
      getBookingDatesRestrictions({
        data: availabilityData,
        selectedRange,
        minDate,
        maxDate,
        disabledDay,
      }),
    [availabilityData, maxDate, minDate, selectedRange, disabledDay],
  );

  return (
    <DateRangePickerInputView
      {...rest}
      value={value}
      error={!!error}
      helperText={error}
      required={required}
      disabledDay={restrictedDisabledDay}
      minDate={restrictedMinDate}
      maxDate={restrictedMaxDate}
      onShownDateChange={debouncedShownDateChangeHandler}
      loadingRanges={loadingRanges}
      onAccept={onAccept}
      onChange={handleSelectedRangeChange}
      slots={{
        picker: BookingDateRangePicker,
      }}
      slotProps={{
        field: { slotProps: { field: { onBlur } } },
        footer: {
          subtitle,
        },
        picker: { slotProps: { day: { getDayAttributes } } },
      }}
    />
  );
};
