import { FC, useCallback } from 'react';

import { Box, generateUtilityClasses, IconButton, IconButtonProps, useTheme } from '@mui/material';
import { NextImage } from '@packages/components';
import { getUrlWithTemplate } from '@packages/helpers';
import { LISTING_ITEM_IMAGE_WIDTH } from 'application/constants';
import { Typography } from 'components/Typography';
import { IcoMoon } from 'custom/IcoMoon';
import { useTranslate } from 'hooks/index';
import WithTitleTemplate from 'templates/WithTitleTemplate';
import { ITheme, styled } from 'theme/index';
import RatingView, { RatingViewProps } from 'views/RatingView';

import { DistancePOIStack, DistancePOIStackProps } from '../DistancePOIStack';

const PREFIX = 'SearchPOIItem';

export const SearchPOIItemClasses = generateUtilityClasses(PREFIX, [
  'wrapper',
  'backSection',
  'rightSection',
  'contentHeader',
  'actionButton',
]);

const StyledSearchPOIItem = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
  shouldForwardProp: (p) => p !== 'selected',
})<{ selected: boolean }>(({ theme, selected }) => {
  return {
    [`&.${SearchPOIItemClasses.wrapper}`]: {
      width: '100%',
      display: 'flex',
      position: 'relative',
      textDecoration: 'none',
      color: theme.palette.text.primary,
      background: theme.palette.primary.background,
      borderRadius: 16,
      '&:hover': {
        cursor: 'pointer',
      },
    },
    [`& .${SearchPOIItemClasses.backSection}`]: {
      width: 194,
      height: '100%',
      position: 'absolute',
      overflow: 'hidden',
      borderRadius: 16,
      zIndex: -1,
      background: theme.palette.actions.background,
    },
    [`& .${SearchPOIItemClasses.rightSection}`]: {
      height: '100%',
      flexBasis: 230,
      flexGrow: 1,
      flexShrink: 0,
      backgroundColor: theme.palette.background.default,
      border: '1px solid',
      borderColor: selected ? theme.palette.primary.main : theme.palette.primary.border,
      borderRadius: 16,
      padding: theme.spacing(2),
      overflow: 'hidden',
    },
    [`& .${SearchPOIItemClasses.contentHeader}`]: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
    },
    [`& .${SearchPOIItemClasses.actionButton}`]: {
      marginTop: theme.spacing(0.5),
    },
  };
});

export type SearchPOIItemProps = {
  title: string;
  distanceMeters?: number;
  src?: string;
  selected?: boolean;
  onDeleteClick?: () => void;
  rating?: RatingViewProps['rating'];
  reviewsCount?: RatingViewProps['reviewsCount'];
  onClick: () => void;
} & Pick<DistancePOIStackProps, 'travelMinutes'>;

export const SearchPOIItem: FC<SearchPOIItemProps> = (props) => {
  const {
    src,
    title,
    distanceMeters,
    onDeleteClick,
    travelMinutes,
    rating,
    reviewsCount,
    selected = false,
    onClick,
  } = props;
  const theme = useTheme<ITheme>();

  const translate = useTranslate({ basePath: 'features.poi' });

  const handleOnDelete = useCallback<NonNullable<IconButtonProps['onClick']>>(
    (e) => {
      e.preventDefault();
      e.stopPropagation(); // otherwise it triggers 'set active' action
      if (onDeleteClick) {
        onDeleteClick();
      }
    },
    [onDeleteClick],
  );

  return (
    <StyledSearchPOIItem
      className={SearchPOIItemClasses.wrapper}
      selected={selected}
      onClick={onClick}
    >
      <Box className={SearchPOIItemClasses.backSection}>
        {src ? (
          <NextImage
            src={getUrlWithTemplate({ url: src, width: LISTING_ITEM_IMAGE_WIDTH })}
            fill
            style={{ objectFit: 'cover' }}
            alt="Listing image"
            // image is reused from browser cache when it loads in listing item
            unoptimized
          />
        ) : null}
      </Box>
      <Box className={SearchPOIItemClasses.rightSection}>
        <Box className={SearchPOIItemClasses.contentHeader}>
          <Box sx={{ overflow: 'hidden' }}>
            {rating ? (
              <RatingView
                rating={rating}
                reviewsCount={reviewsCount}
                typographyProps={{ sx: { color: theme.palette.text.light }, variant: 'body1' }}
                sx={{ mb: 1 }}
              />
            ) : null}
            <Typography
              variant="subtitle2"
              sx={{ textOverflow: 'ellipsis', overflow: 'hidden', whiteSpace: 'nowrap' }}
              withTooltip
            >
              {title}
            </Typography>
          </Box>
          {typeof onDeleteClick !== 'undefined' ? (
            <IconButton onClick={handleOnDelete} sx={{ ml: 2 }}>
              <IcoMoon icon="delete" />
            </IconButton>
          ) : null}
        </Box>

        {distanceMeters ? (
          <WithTitleTemplate
            sx={{ my: 1 }}
            title={
              <Typography variant="small2">
                {translate('labels.distance_km', {
                  km: Math.round((10 * distanceMeters) / 1000) / 10, // round to .0
                })}
              </Typography>
            }
          >
            <IcoMoon icon="map-distance" size="small" color={theme.palette.primary.main} />
          </WithTitleTemplate>
        ) : null}

        <Box sx={{ mt: 1 }}>
          <DistancePOIStack travelMinutes={travelMinutes} typographyProps={{ variant: 'small2' }} />
        </Box>
      </Box>
    </StyledSearchPOIItem>
  );
};
