import { forwardRef, useMemo } from 'react';

import { generateUtilityClasses, Tooltip } from '@mui/material';
import { clsx } from 'clsx';
import { styled } from 'theme/index';
import {
  DateRangePickerDay,
  DateRangePickerDayClasses,
  DateRangePickerDayProps,
} from 'views/DateRangePickerInputView';

import { useTooltipTitle } from './useTooltipTitle';

const PREFIX = 'BookingDateRangePickerDay';

export const PickerDayClasses = generateUtilityClasses(PREFIX, ['inactive']);

const StyledDateRangePickerDay = styled(DateRangePickerDay, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(() => {
  return {
    [`&.${DateRangePickerDayClasses.root}`]: {
      width: '100%',
      height: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
  };
});

export interface DayAttributes {
  minNights: number | null;
  checkoutOnly: boolean;
}

export interface PickerDayProps extends DateRangePickerDayProps {
  getDayAttributes?: (date: Date) => DayAttributes;
}

export const PickerDay = forwardRef<HTMLDivElement, PickerDayProps>((props, ref) => {
  const { date, format, className, sx, getDayAttributes } = props;

  const attributes = useMemo(() => getDayAttributes?.(date), [date, getDayAttributes]);

  const { minNights = null, checkoutOnly = false } = attributes || {};
  const inactive = minNights || checkoutOnly;

  const tooltipTitle = useTooltipTitle({ minNights, checkoutOnly });

  return (
    <Tooltip ref={ref} title={tooltipTitle} enterTouchDelay={50} leaveTouchDelay={1500}>
      <StyledDateRangePickerDay
        date={date}
        format={format}
        className={clsx(className, {
          [PickerDayClasses.inactive]: inactive,
        })}
        sx={sx}
      />
    </Tooltip>
  );
});

PickerDay.displayName = 'BookingDateRangePickerDay';
