import { FC, useCallback, useEffect, useMemo, useRef } from 'react';

import { Box, Divider, generateUtilityClasses, useTheme } from '@mui/material';
import { getSelectNumericChoices, useBoolean } from '@packages/helpers';
import { Identifier, StyledRootProps } from '@packages/types';
import {
  ACCOMMODATES_SOURCE,
  BOOKING_RANGE_SOURCE,
  CHECKIN_AT_SOURCE,
  CHECKOUT_AT_SOURCE,
  reservationRoute,
} from 'application/constants';
import { BookInfoTable } from 'custom/BookInfoTable';
import { IcoMoon } from 'custom/IcoMoon';
import { Loader } from 'custom/Loader';
import { useFilters } from 'hooks/filters/useFilters';
import { useTranslate } from 'hooks/index';
import {
  QUOTES_SOURCES,
  QuotesFormData,
  quotesFormValidationSchema as quotesRequestSchema,
  useGetQuotes,
} from 'hooks/queries/useGetQuotes';
import { useForm } from 'hooks/useForm';
import { BookingDateRangePickerInput } from 'inputs/BookingDateRangePickerInput';
import { SelectField } from 'inputs/SelectField';
import { get, isEqual } from 'lodash';
import { useRouter } from 'next/router';
import styled from 'theme/styled';
import { FormView } from 'views/FormView';
import { getMessagesFromError } from 'views/FormView/helpers/getMessagesFromError';
import { setSubmissionErrors } from 'views/FormView/helpers/setSubmissionErrors';

import { bookInfoFormValidationSchema } from './schema';

import { Button } from '../../buttons/Button';
import { Link } from '../../links/Link';
import { Typography } from '../../Typography';
import { LowerPriceView } from '../../views/LowerPriceView';
import { PriceView } from '../../views/PriceView';
import { RatingView } from '../../views/RatingView';

export interface BookInfoFormProps extends StyledRootProps {
  rating?: number;
  reviewsCount?: number;
  basePrice: number;
  discount?: number;
  maxAccommodatesCount?: number;
  listingId: Identifier;
  listingSlug: string;
  onCloseModal?: () => void;
}

const PREFIX = 'BookInfoForm';

export const BookInfoFormClasses = generateUtilityClasses(PREFIX, ['button']);

const StyledBookInfoForm = styled(FormView, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const mobileBreakpoint = theme.breakpoints.down('md');

  return {
    [`& .${BookInfoFormClasses.button}`]: {
      marginTop: theme.spacing(2),

      [mobileBreakpoint]: {
        order: 1, // move it to the bottom on mobile
      },
    },
  };
});

export const BookInfoForm: FC<BookInfoFormProps> = (props) => {
  const {
    rating,
    reviewsCount,
    basePrice,
    discount,
    sx,
    className,
    maxAccommodatesCount = 1,
    listingId,
    listingSlug,
    onCloseModal,
  } = props;

  const { value: isOpenPicker, setTrue: openPicker, setFalse: closePicker } = useBoolean(false);

  const theme = useTheme();
  const router = useRouter();
  const translate = useTranslate({ basePath: 'pages.listing.form' });

  const { allFilters, setFilters } = useFilters({
    independentFiltersSlugs: [BOOKING_RANGE_SOURCE],
    includeFilterAttribute: (slug) => slug === ACCOMMODATES_SOURCE,
    defaultValues: {
      [ACCOMMODATES_SOURCE]: 1,
    },
  });

  const defaultValues = useMemo(() => {
    return {
      [QUOTES_SOURCES['checkin_at']]: allFilters[BOOKING_RANGE_SOURCE][CHECKIN_AT_SOURCE],
      [QUOTES_SOURCES['checkout_at']]: allFilters[BOOKING_RANGE_SOURCE][CHECKOUT_AT_SOURCE],
      [QUOTES_SOURCES['guest_count']]: get(allFilters, ACCOMMODATES_SOURCE),
    };
  }, [allFilters]);

  const formMethods = useForm({
    defaultValues,
    validationSchema: bookInfoFormValidationSchema,
  });

  const {
    formState: { isValid: isFormValid, isDirty: isFormDirty },
    watch,
    trigger,
    setError,
  } = formMethods;

  // Watch form values to update quotes when values changed
  const [from, to, accommodates] = watch([
    QUOTES_SOURCES['checkin_at'],
    QUOTES_SOURCES['checkout_at'],
    QUOTES_SOURCES['guest_count'],
  ]);

  const quotesParams = useMemo(() => {
    return {
      [QUOTES_SOURCES['checkin_at']]: from,
      [QUOTES_SOURCES['checkout_at']]: to,
      [QUOTES_SOURCES['guest_count']]: accommodates,
    };
  }, [accommodates, from, to]);

  const parseQuotesResult = useMemo(() => {
    return quotesRequestSchema.safeParse(quotesParams);
  }, [quotesParams]);

  const prevQuotesParamsRef = useRef<unknown>();

  const isSelectedDates = !!from && !!to;

  useEffect(() => {
    if (isEqual(prevQuotesParamsRef.current, quotesParams)) return;

    const {
      [QUOTES_SOURCES['checkin_at']]: from,
      [QUOTES_SOURCES['checkout_at']]: to,
      [QUOTES_SOURCES['guest_count']]: accommodates,
    } = quotesParams;

    const newFilters = {
      [BOOKING_RANGE_SOURCE]: {
        [CHECKIN_AT_SOURCE]: from,
        [CHECKOUT_AT_SOURCE]: to,
      },
      [ACCOMMODATES_SOURCE]: accommodates,
    };

    prevQuotesParamsRef.current = quotesParams;

    setFilters(() => newFilters, { replace: true });
  }, [quotesParams, setFilters]);

  const isQuotesRequestEnabled = parseQuotesResult.success;

  const {
    data: quotesData,
    isError,
    error,
    isFetching,
  } = useGetQuotes(
    {
      data: parseQuotesResult.success && parseQuotesResult.data,
      listingId,
    },
    {
      enabled: isQuotesRequestEnabled,
      retryOnMount: false,
    },
  );

  useEffect(() => {
    if (isError) {
      const { errors } = getMessagesFromError(error);
      setSubmissionErrors(errors, setError);
    }
  }, [error, isError, setError]);

  useEffect(() => {
    /**
     * We manually trigger validation, because `mode: "onChange"`
     * doesn't work with validation schema for some reason
     */
    if (isFormDirty) {
      trigger();
    }
  }, [from, to, accommodates, trigger, isFormDirty]);

  const handleRequestBooking = useCallback(
    (values: QuotesFormData) => {
      const { checkin_at, checkout_at, guest_count } = values;

      router.push({
        pathname: reservationRoute.getPathname(listingSlug),
        query: {
          [CHECKIN_AT_SOURCE]: checkin_at,
          [CHECKOUT_AT_SOURCE]: checkout_at,
          [ACCOMMODATES_SOURCE]: guest_count,
        },
      });
    },
    [listingSlug, router],
  );

  const shouldRenderData = isFormValid && !isError && !isFetching && !!quotesData;
  const shouldDisableSubmit = !isFormValid || isError || isFetching;

  return (
    <StyledBookInfoForm
      {...formMethods}
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
        ...sx,
      }}
      className={className}
      onSubmit={handleRequestBooking}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          minHeight: 20,
        }}
      >
        {basePrice ? <PriceView prefix="from" variant="per_night" price={basePrice} /> : null}
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          {rating ? (
            <RatingView
              rating={rating}
              typographyProps={{
                variant: 'body1',
              }}
            />
          ) : null}
          {reviewsCount ? (
            <Link
              href="#review"
              onClick={() => {
                if (onCloseModal) {
                  onCloseModal();
                }
              }}
              sx={{
                color: theme.palette.text.primary,
                ml: 0.5,
                textDecoration: 'none',
              }}
            >
              <Typography variant="buttonUnderline">
                {translate('{{count}}_reviews', { count: reviewsCount })}
              </Typography>
            </Link>
          ) : null}
        </Box>
      </Box>
      <Divider sx={{ mt: 2, mb: 2 }} />
      <BookingDateRangePickerInput
        listingId={listingId}
        fromName={QUOTES_SOURCES['checkin_at']}
        toName={QUOTES_SOURCES['checkout_at']}
        label={translate('checkin_checkout')}
        minDate={new Date()}
        sx={{ mt: 2 }}
        open={isOpenPicker}
        onClose={closePicker}
      />
      {/* TODO: The temporary solution according to PM decision, we will have GuestSelectInput later */}
      <SelectField
        name={QUOTES_SOURCES['guest_count']}
        sx={{ mt: 1 }}
        label={translate('guests')}
        startIcon={<IcoMoon icon="user" />}
        items={getSelectNumericChoices({ min: 1, max: maxAccommodatesCount, step: 1 })}
        resettable={false}
      />
      {/* <GuestSelectInput
          name={SOURCES.guests}
          sx={{ mt: 1 }}
          label={getTranslate('guests')}
        /> */}

      <Box sx={{ flex: '1 1 auto' }} />
      <Box sx={{ mt: 3 }}>
        {isFetching && <Loader />}
        {shouldRenderData && <BookInfoTable quotesData={quotesData} />}
      </Box>
      {!isSelectedDates ? (
        <Button
          onClick={openPicker}
          variant="contained"
          className={BookInfoFormClasses.button}
          fullWidth
        >
          {translate('check_availability')}
        </Button>
      ) : (
        <Button
          type="submit"
          disabled={shouldDisableSubmit}
          variant="contained"
          className={BookInfoFormClasses.button}
          fullWidth
        >
          {translate('book_now')}
        </Button>
      )}
      {typeof discount !== 'undefined' && (
        <LowerPriceView
          discount={discount}
          sx={{
            mt: 2,
          }}
        />
      )}

      <Box
        sx={{
          mt: 2,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          textAlign: 'center',
        }}
      >
        <Typography variant="body1" color="text.light" sx={{ mb: 1 }}>
          {translate('wont_be_charget')}
        </Typography>
        <Typography variant="body1" color="text.light">
          {translate('may_require_deposit')}
        </Typography>
      </Box>
    </StyledBookInfoForm>
  );
};

export default BookInfoForm;
