import { getFormattedDateValue } from '@packages/helpers';
import { ListingAvailabilityRange } from 'hooks/queries/useGetListingAvailabilities';

import { GetAvailabilityRangeFromDateReturn } from '../getAvailabilityRangeFromDate';

export type GetFormattedAvailabilityRangeParams = {
  range: GetAvailabilityRangeFromDateReturn;
  format: string;
};

export type GetFormattedAvailabilityRangeReturn = ListingAvailabilityRange;

export const getFormattedAvailabilityRange = (
  params: GetFormattedAvailabilityRangeParams,
): GetFormattedAvailabilityRangeReturn => {
  const { range, format } = params;

  const [start, end] = range;

  const formattedStartDate = getFormattedDateValue({
    value: start,
    format,
    fallbackValue: '',
  });

  const formattedEndDate = getFormattedDateValue({
    value: end,
    format,
    fallbackValue: '',
  });

  return {
    from: formattedStartDate,
    to: formattedEndDate,
  };
};
