import { FC } from 'react';

import { Box, useMediaQuery, useTheme } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { GridContainer } from 'templates/GridContainer';
import { GridItem } from 'templates/GridItem';

import { Typography } from '../../Typography';
import RequirementView from '../RequirementView';

export interface HouseRulesSectionProps extends StyledRootProps {
  requirements: Array<{
    label: string;
    isAllowed: boolean;
  }>;
}

export const HouseRulesSection: FC<HouseRulesSectionProps> = (props) => {
  const { requirements, sx, className } = props;

  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));

  return (
    <Box sx={sx} className={className}>
      <Typography variant="h4" sx={{ mb: 6 }}>
        {'custom.house_rules'}
      </Typography>
      <GridContainer columnSpacing={3} rowSpacing={isDesktop ? 4 : 3}>
        {requirements.map((item, index) => {
          const { label, isAllowed } = item;
          return (
            <GridItem
              key={label + index /* Add 'index' because labels may be technically same */}
              md={4}
            >
              <RequirementView allowed={isAllowed} label={label} />
            </GridItem>
          );
        })}
      </GridContainer>
    </Box>
  );
};

export default HouseRulesSection;
