import { zodBooleanSchema } from '@packages/providers';
import { addMinutes, isValid } from 'date-fns';
import { z, ZodIssueCode } from 'zod';

import { LISTING_AVAILABILITY_STATUSES } from './constants';

export const listingAvailabilityResponseSchema = z
  .array(
    z.object({
      date: z.string().transform((value, ctx) => {
        const userTimezoneDate = new Date(value);
        if (isValid(userTimezoneDate)) {
          const timezoneOffset = userTimezoneDate.getTimezoneOffset();
          return addMinutes(userTimezoneDate, timezoneOffset);
        }
        ctx.addIssue({
          code: ZodIssueCode.invalid_date,
        });
        return z.NEVER;
      }),
      status: z.nativeEnum(LISTING_AVAILABILITY_STATUSES),
      min_nights: z.number().nullable(),
      max_nights: z.number().nullable(),
      is_checkin_available: zodBooleanSchema,
      is_checkout_available: zodBooleanSchema,
    }),
  )
  .default([]);
