import { FC } from 'react';

import { Box, Grid, Theme, Typography, useMediaQuery, useTheme } from '@mui/material';
import { useTranslate } from 'hooks/index';
import { ModalBase, ModalBaseProps } from 'modals/ModalBase';
import { AmenityItem } from 'views/AmenitiesFieldView/AmenitiesFieldView';
import { AmenityView } from 'views/AmenityView';

interface AmenitiesModalProps extends ModalBaseProps {
  amenities: AmenityItem[];
}

export const AmenitiesModal: FC<AmenitiesModalProps> = (props) => {
  const { amenities, ...baseModalProps } = props;
  const theme = useTheme();
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));
  const translate = useTranslate({ basePath: 'pages.listing' });

  return (
    <ModalBase
      fullScreen={isMobile}
      title={translate('titles.amenities')}
      PaperProps={{ sx: { minWidth: { md: 600 } } }}
      {...baseModalProps}
    >
      <Box mt={{ xs: 1, md: 3 }}>
        <Grid container rowSpacing={{ xs: 3, md: 2 }}>
          {amenities.map((amenity, index) => {
            const { id, title, ...amenityItemProps } = amenity;

            const amenitiesLength = amenities.length;

            const countItemsWithoutBorder = amenitiesLength % 2 ? 1 : 2;

            const isShowBorder = isMobile
              ? index !== amenitiesLength - 1
              : index < amenitiesLength - countItemsWithoutBorder;

            return (
              <Grid
                item
                xs={12}
                key={id}
                md={6}
                sx={{
                  borderBottom: isShowBorder
                    ? `1px solid ${theme.palette.primary.background}`
                    : 'none',
                  pb: { xs: 3, md: 2 },
                }}
              >
                <AmenityView
                  title={<Typography variant="subtitle1">{title}</Typography>}
                  {...amenityItemProps}
                />
              </Grid>
            );
          })}
        </Grid>
      </Box>
    </ModalBase>
  );
};
