import { FC } from 'react';

import { useEffectOnValueChange } from '@packages/helpers';
import { useGoogleMap } from '@react-google-maps/api';
import { usePoiContext } from 'components/poi/components/PoiContext';

import { MapPoiPoint } from './MapPoiPoint';

export const MapPoiPoints: FC = (props) => {
  const { activeId, poisById, clearActive, setActiveId } = usePoiContext();
  const map = useGoogleMap();

  useEffectOnValueChange({
    value: activeId,
    onChange: () => {
      if (!activeId || !map) return;
      const poi = poisById[activeId] || null;
      if (!poi) return;
      const { latitude, longitude } = poi;
      map.panTo({ lat: latitude, lng: longitude });
    },
  });

  return (
    <>
      {Object.entries(poisById).map(([id, poiData]) => {
        const {
          latitude: lat,
          longitude: lng,
          walk_meters = 0,
          walk_seconds = 0,
          bicycle_seconds = 0,
          drive_seconds = 0,
          drive_meters = 0,
          address,
          image: { src = '' } = {},
        } = poiData;
        return (
          <MapPoiPoint
            key={id}
            position={{ lat, lng }}
            selected={activeId === id}
            onOpen={() => {
              setActiveId(id);
            }}
            onClose={clearActive}
            src={src}
            poiProps={{
              title: address,
              travelMinutes: {
                car: drive_seconds / 60,
                bicycle: bicycle_seconds / 60,
                walk: walk_seconds / 60,
              },
              distanceKm:
                /* BE may not return some of distances. So we use one of them */
                (walk_meters || bicycle_seconds || drive_meters) / 1000,
              onCloseClick: clearActive,
            }}
          />
        );
      })}
    </>
  );
};

export default MapPoiPoints;
