import { FC, useCallback, useMemo, useState } from 'react';

import { fetchClient } from '@packages/providers';
import { Identifier } from '@packages/types';
import { useQuery } from '@tanstack/react-query';
import { PLACES_AUTOCOMPLETE_RESOURCES } from 'application/resources';
import { IcoMoon } from 'components/custom/IcoMoon';
import { debounce } from 'lodash';
import { theme } from 'theme/index';
import { AutocompleteInputView, AutocompleteInputViewProps } from 'views/AutocompleteInputView';

import { PlacesAutocompleteTypes, zodPlacesAutocompleteSchemas } from '../api/places-autocomplete';

type ChoiceType = PlacesAutocompleteTypes['base'][number];

export interface PlaceInputProps extends Partial<AutocompleteInputViewProps<ChoiceType>> {
  onAddPlace: (params: { apiId: Identifier }) => void;
  listingId: Identifier;
}

export const PlaceInput: FC<PlaceInputProps> = (props) => {
  const { onAddPlace, listingId, ...rest } = props;

  const [inputStr, setInputStr] = useState('');
  const placesAutocompleteResult = useQuery({
    queryKey: [PLACES_AUTOCOMPLETE_RESOURCES, inputStr, listingId],
    queryFn: async () => {
      const response = await fetchClient.get({
        pathname: PLACES_AUTOCOMPLETE_RESOURCES,
        input: inputStr,
        listing_id: listingId,
      });

      return zodPlacesAutocompleteSchemas.base.parse(response.data?.data);
    },
    enabled: !!inputStr && inputStr.length >= 2,
  });

  const onInputChange = useCallback<
    NonNullable<AutocompleteInputViewProps<ChoiceType>['onInputChange']>
  >((_, value) => {
    setInputStr(value);
  }, []);

  const debouncedOnInputChange = useMemo(() => debounce(onInputChange, 500), [onInputChange]);

  const getOptionLabel = useCallback<
    NonNullable<AutocompleteInputViewProps<ChoiceType>['getOptionLabel']>
  >((option) => {
    return option.description || '';
  }, []);

  const onChange = useCallback<NonNullable<AutocompleteInputViewProps<ChoiceType>['onChange']>>(
    (_, choice) => {
      if (choice) {
        onAddPlace({ apiId: choice.id });
        setInputStr('');
      }
    },
    [onAddPlace],
  );

  return (
    <AutocompleteInputView
      forcePopupIcon={false}
      clearOnEscape
      options={placesAutocompleteResult.data || []}
      loading={placesAutocompleteResult.isFetching}
      value={null} // We handle selected value and keep input empty
      onChange={onChange}
      onInputChange={debouncedOnInputChange}
      getOptionLabel={getOptionLabel}
      startIcon={<IcoMoon icon="search" color={theme.palette.text.secondary} />}
      filterOptions={
        /* To not filter options on FE. */
        (options) => options
      }
      {...rest}
    />
  );
};

export default PlaceInput;
