import { FC } from 'react';

import { Box, Button, Theme, useMediaQuery } from '@mui/material';
import { useBoolean } from '@packages/helpers';
import { Identifier, StyledRootProps } from '@packages/types';
import { useTranslate } from 'hooks/index';
import { AmenitiesModal } from 'modals/AmenitiesModal';

import { Typography } from '../../Typography';
import { AmenityView, AmenityViewProps } from '../AmenityView';

export interface AmenityItem extends Pick<AmenityViewProps, 'title' | 'icon' | 'isMatched'> {
  id: Identifier;
}

export interface AmenitiesFieldViewProps extends StyledRootProps {
  title?: string;
  items: Array<AmenityItem>;
  maxItems?: number;
}

export const AmenitiesFieldView: FC<AmenitiesFieldViewProps> = (props) => {
  const { items, title, className, maxItems = 10, sx } = props;

  const { value: isModalOpen, setTrue: handleModalOpen, setFalse: handleModalClose } = useBoolean();

  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('sm'));
  const translate = useTranslate();

  return (
    <Box component="section" className={className} sx={sx}>
      {title && (
        <Typography component="h3" variant="subtitle1" sx={{ mb: 3 }}>
          {title}
        </Typography>
      )}

      <Box display="grid" gridTemplateColumns="repeat(auto-fill, 175px)" gap={3}>
        {items.slice(0, maxItems).map((item) => {
          const { id, title, ...amenityItemProps } = item;
          return (
            <AmenityView
              key={id}
              title={<Typography variant="subtitle1">{title}</Typography>}
              {...amenityItemProps}
            />
          );
        })}
      </Box>
      <AmenitiesModal open={isModalOpen} onClose={handleModalClose} amenities={items} />

      {items.length > maxItems && (
        <Button
          onClick={handleModalOpen}
          variant={isMobile ? 'outlined' : 'text'}
          fullWidth={isMobile}
          sx={{
            display: 'block',
            mx: 'auto',
            mt: 4,
            textTransform: 'lowercase',
            ':first-letter': { textTransform: 'uppercase' },
          }}
          color="secondary"
        >
          {translate.root('buttons.show_all_items', { items: translate.root('custom.amenities') })}
        </Button>
      )}
    </Box>
  );
};

export default AmenitiesFieldView;
