import { FC } from 'react';

import { StyledRootProps } from '@packages/types';
import { IcoMoon } from 'custom/IcoMoon';

import { WithTitleTemplate } from '../../templates/WithTitleTemplate';
import { Typography } from '../../Typography';

export interface LocationViewProps extends StyledRootProps {
  label: string;
}

export const LocationView: FC<LocationViewProps> = (props) => {
  const { label, sx, className } = props;

  return (
    <WithTitleTemplate
      title={
        <Typography variant="subtitle1" sx={{ color: 'text.primary' }}>
          {label}
        </Typography>
      }
      sx={sx}
      className={className}
    >
      <IcoMoon icon="pin" />
    </WithTitleTemplate>
  );
};

export default LocationView;
