import { FC } from 'react';

import { Grid, Typography } from '@mui/material';
import { ExpandableWrapper } from 'components/layouts/ExpandableWrapper';
import { useTranslate } from 'hooks';

export type DescriptionSlugs =
  | 'summary'
  | 'space_description'
  | 'access_description'
  | 'neighborhood_description'
  | 'transit_description'
  | 'notes';

export interface DescriptionSectionProps {
  description: Record<string, string>;
}

export const DESCRIPTION_SLUGS = {
  summary: 'summary',
  space: 'space_description',
  access: 'access_description',
  neighborhood: 'neighborhood',
  transit: 'transit',
  notes: 'notes',
} as const;

export const DescriptionSection: FC<DescriptionSectionProps> = (props) => {
  const { description } = props;

  const translate = useTranslate({ basePath: 'pages.listing.description' });

  const items = Object.entries(description);

  return (
    <ExpandableWrapper
      // TODO: improve calculating of the height to not have cut of text
      maxHeight={260}
      isBlur
      slotProps={{
        toggleButton: {
          sx: {
            ml: 0,
            mt: 2,
            padding: 0,
            minWidth: 0,
            height: 'initial',
            minHeight: 'initial',
            '&, :hover': {
              backgroundColor: 'transparent',
              textDecoration: 'underline',
            },
          },
          showMoreText: translate.root('buttons.show_all'),
          showLessText: translate.root('buttons.hide'),
        },
      }}
    >
      <Grid container gap="20px">
        {items.map((item, index) => {
          const slug = item[0] as DescriptionSlugs;

          return (
            <Grid key={index} item xs={12}>
              <Typography variant="h6">{translate(slug)}</Typography>
              <Typography
                mt={2}
                variant="subtitle1"
                sx={{ whiteSpace: 'break-spaces', wordBreak: 'break-word' }}
              >
                {item[1]}
              </Typography>
            </Grid>
          );
        })}
      </Grid>
    </ExpandableWrapper>
  );
};
