import { useMemo } from 'react';

import {
  Box,
  Container,
  Divider,
  generateUtilityClasses,
  SxProps,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import { useBoolean } from '@packages/helpers';
import { zodAttributeSlugSchema, zodListingSchemas } from '@packages/providers';
import {
  ACCOMMODATES_SOURCE,
  BASE_PRICE_SLUG,
  BATHROOMS_SOURCE,
  BEDROOMS_SOURCE,
  BOOKING_RANGE_SOURCE,
  CANCELLATION_POLICY_SOURCE,
  CHECKIN_AT_SOURCE,
  CHECKOUT_AT_SOURCE,
  HOUSE_RULES_SOURCE,
  LOCATION_SLUG,
  PURPOSE_TYPES,
  RATING_COUNT_SLUG,
  RATING_SLUG,
} from 'application/constants';
import Button from 'buttons/Button';
import { AmenitiesSection } from 'components/layouts/AmenitiesSection';
import { Link } from 'components/links/Link';
import { AnchorMenu } from 'components/menus/AnchorMenu';
import { ClosestPOISwiper, PoiContextProvider } from 'components/poi';
import { ListingReviewsSection } from 'components/sections/ListingReviewsSection';
import { RecommendedListingsSection } from 'components/sections/RecommendedListingsSection';
import { Typography } from 'components/Typography';
import { AnchorSection } from 'custom/AnchorSection';
import { BookInfoForm } from 'custom/BookInfoForm';
import { IcoMoon } from 'custom/IcoMoon';
import { getParsedFilters } from 'helpers/filters/getParsedFilters';
import { getFilterValues } from 'helpers/getFilterValues';
import { getNormalizedQuoteData } from 'helpers/getNormalizedQuoteData';
import { getValueFromFilter } from 'helpers/getValueFromFilter';
import { useGetPriceInCurrentCurrency } from 'helpers/useGetPriceInCurrentCurrency';
import { useFilters } from 'hooks/filters/useFilters';
import { useGetFilterAttributes, useTranslate } from 'hooks/index';
import { useGetFilterLabels } from 'hooks/queries/useGetFilterLabels';
import {
  QUOTES_SOURCES,
  quotesFormValidationSchema,
  useGetQuotes,
} from 'hooks/queries/useGetQuotes';
import { useGetRecommendedListings } from 'hooks/queries/useGetRecommendedListings';
import { get, isEmpty } from 'lodash';
import { BookInfoModal } from 'modals/BookInfoModal';
import { WithStickySectionWrapper } from 'templates/WithStickySectionWrapper';
import { HEADER_HEIGHT, MOBILE_FIXED_BOTTOM_SECTION_HEIGHT, styled } from 'theme/index';
import { AmenitiesStackView } from 'views/AmenitiesStackView';
import { BlockWrapper } from 'views/BlockWrapper';
import { CancellationPolicySection } from 'views/CancellationPolicySection';
import { HouseRulesSection } from 'views/HouseRulesSection';
import ListingGalleryView from 'views/ListingGalleryView/ListingGalleryView';
import { LocationView } from 'views/LocationView';
import PriceView from 'views/PriceView';

import { ListingMap } from './components';
import { DESCRIPTION_SLUGS, DescriptionSection } from './components/DescriptionSection';
import { useShareController } from './useShareController';

/**
 * Because BE blockers
 */

const PREFIX = 'ListingPage';

export const ListingPageClasses = generateUtilityClasses(PREFIX, [
  'anchorMenuWrapper',
  'mainInfoSection',
  'mainInfoSectionActions',
  'fixedMobileBookSection',
  'POISection',
]);

const StyledListingPage = styled('div', {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const mobileBreakpoint = theme.breakpoints.down('md');
  return {
    [`& .${ListingPageClasses.anchorMenuWrapper}`]: {
      position: 'relative',
      top: '-50%',
      transform: 'translateY(-50%)',
      display: 'flex',
      justifyContent: 'center',
      [mobileBreakpoint]: {
        display: 'none',
      },
    },
    [`& .${ListingPageClasses.mainInfoSection}`]: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      [mobileBreakpoint]: {
        display: 'block',
        marginTop: theme.spacing(3),
      },
    },
    [`& .${ListingPageClasses.mainInfoSectionActions}`]: {
      display: 'flex',
      justifyContent: 'space-between',
      marginTop: 0,
      gap: theme.spacing(1),
      [mobileBreakpoint]: {
        marginTop: theme.spacing(2),
        justifyContent: 'end',
      },
    },

    [`& .${ListingPageClasses.fixedMobileBookSection}`]: {
      position: 'fixed',
      zIndex: theme.zIndex.appBar,
      height: MOBILE_FIXED_BOTTOM_SECTION_HEIGHT,
      backgroundColor: theme.palette.background.default,
      width: '100%',
      bottom: 0,
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      padding: theme.spacing(0, 2),
      borderTop: '1px solid',
      borderColor: theme.palette.primary.background,
    },
    [`& .${ListingPageClasses.POISection}`]: {
      marginTop: theme.spacing(1),
    },
  };
});

export interface ListingPageProps {
  data: typeof zodListingSchemas.show._output;
  url: string;
}

export function ListingPage(props: ListingPageProps) {
  const { data, url } = props;
  const {
    id,
    city,
    address,
    country,
    gallery,
    title,
    agent,
    description,
    formatted_filters = [],
    slug,
    currency,
    tour_id,
  } = data;

  const { data: filterAttributes } = useGetFilterAttributes({
    purpose: PURPOSE_TYPES['searching'],
  });

  const [accommodates, bedrooms, bathrooms, basePrice, rating, ratingCount] = getFilterValues({
    formattedFilters: formatted_filters,
    slugs: [
      ACCOMMODATES_SOURCE,
      BEDROOMS_SOURCE,
      BATHROOMS_SOURCE,
      BASE_PRICE_SLUG,
      RATING_SLUG,
      RATING_COUNT_SLUG,
    ],
  });

  const { selectedFilters: parsedFilters } = getParsedFilters({
    filter: {
      [ACCOMMODATES_SOURCE]: accommodates?.value,
      [BEDROOMS_SOURCE]: bedrooms?.value,
    },
    filterAttributes: filterAttributes || [],
    independentFiltersSlugs: [],
  });

  const accommodatesValue = get(parsedFilters, ACCOMMODATES_SOURCE) as number | undefined;
  const bedroomsValue = get(parsedFilters, BEDROOMS_SOURCE) as number[] | undefined;

  const priceValue = basePrice?.value ? Number(basePrice?.value) : 0;
  const getPrice = useGetPriceInCurrentCurrency();

  const theme = useTheme();

  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const translate = useTranslate({ basePath: 'pages.listing' });

  const { allFilters } = useFilters({
    independentFiltersSlugs: [BOOKING_RANGE_SOURCE],
    includeFilterAttribute: (slug) => slug === ACCOMMODATES_SOURCE,
    defaultValues: {
      [ACCOMMODATES_SOURCE]: 1,
    },
  });

  const checkinAtFilter = allFilters[BOOKING_RANGE_SOURCE][CHECKIN_AT_SOURCE];
  const checkoutAtFilter = allFilters[BOOKING_RANGE_SOURCE][CHECKOUT_AT_SOURCE];

  const isSelectedDates = !!checkinAtFilter && !!checkoutAtFilter;

  const parseQuotesResult = useMemo(() => {
    const quotesParams = {
      [QUOTES_SOURCES['checkin_at']]: checkinAtFilter,
      [QUOTES_SOURCES['checkout_at']]: checkoutAtFilter,
      [QUOTES_SOURCES['guest_count']]: accommodates,
    };

    return quotesFormValidationSchema.safeParse(quotesParams);
  }, [accommodates, checkinAtFilter, checkoutAtFilter]);

  const { data: recommendedListings = [] } = useGetRecommendedListings({
    filter: {
      exclude_ids: [id],
      accommodates: accommodatesValue,
      bedrooms: bedroomsValue,
    },
  });

  const { data: quotesData } = useGetQuotes(
    {
      listingId: id,
      data: {
        [QUOTES_SOURCES['checkin_at']]: checkinAtFilter,
        [QUOTES_SOURCES['checkout_at']]: checkoutAtFilter,
        [QUOTES_SOURCES['guest_count']]: get(allFilters, ACCOMMODATES_SOURCE),
      },
    },
    { enabled: parseQuotesResult.success, retryOnMount: false },
  );

  const amenitiesSlug: typeof zodAttributeSlugSchema._output = 'amenities';
  const amenitiesFormattedFilters = formatted_filters
    .filter((v) => v.filter_attribute?.slug === amenitiesSlug)
    .sort((a, b) => {
      if (a?.title && b?.title) {
        return a.title.localeCompare(b.title);
      }

      return 1;
    });

  const cancellationPolicyFormattedFilters = formatted_filters.filter(
    (v) => v.filter_attribute?.slug === CANCELLATION_POLICY_SOURCE,
  );

  const houseRulesFormattedFilters = formatted_filters.filter(
    (v) => v.filter_attribute?.slug === HOUSE_RULES_SOURCE,
  );

  const { value: latLocationCoordinate } = getValueFromFilter({
    filtersArray: formatted_filters,
    slug: LOCATION_SLUG,
  });

  const { value: lngLocationCoordinate } = getValueFromFilter({
    filtersArray: formatted_filters,
    slug: LOCATION_SLUG,
    selectSource: 'amount_max',
  });

  const { filterAttribute: houseRulesFA } = getValueFromFilter({
    filtersArray: filterAttributes || [],
    slug: HOUSE_RULES_SOURCE,
  });

  const { data: houseRulesFAs = [] } = useGetFilterLabels(houseRulesFA?.id || '');

  const houseRules = houseRulesFAs?.map((rule) => {
    const isAllowed = houseRulesFormattedFilters.some((el) => el.slug === rule.slug);
    return {
      ...rule,
      isAllowed,
    };
  });

  const { averagePricePerNight, currencyCode } = getNormalizedQuoteData({ data: quotesData });
  const averagePriceInCurrentCurrency = getPrice({
    amount: averagePricePerNight,
    initialCurrencyCode: currencyCode,
  });
  const basePriceInCurrentCurrency = getPrice({
    amount: priceValue,
    initialCurrencyCode: currency?.code || '',
  });

  const resultData = {
    id,
    rating: Number(rating?.value) || 0, // use default 0 to avoid NaN
    reviewsCount: Number(ratingCount?.value) || 0, // use default 0 to avoid NaN
    location: { lat: Number(latLocationCoordinate), lng: Number(lngLocationCoordinate) },
    basePrice: averagePricePerNight ? averagePriceInCurrentCurrency : basePriceInCurrentCurrency,
    discount: undefined, // feature
    agentFirstName: agent?.first_name || '',
    agentLastName: agent?.last_name || '',
    avatarSrc: agent?.avatar?.src || '',
    title,
    tourLink: tour_id,
    zip: undefined,
    mainAmenities: [
      { id: '1', value: bedrooms?.value, label: bedrooms?.filterAttribute?.title },
      { id: '2', value: bathrooms?.value, label: bathrooms?.filterAttribute?.title },
      { id: '3', value: accommodatesValue, label: translate.root('custom.guest') },
    ],
    address,
    cityName: city?.title || '',
    countryName: country?.title || '',
    gallery,
    thumbnailSrc: gallery?.[0]?.src,
    description,
    similarListings: recommendedListings,
    cancelationPolicyInfo: cancellationPolicyFormattedFilters.map((v) => v.amount || ''),
    houseRules: houseRules.map((rule) => ({
      label: rule.title || '',
      isAllowed: rule.isAllowed,
    })),
    slug,
    amenities: amenitiesFormattedFilters.map((v) => {
      /* We should only existed ameninites for now */
      return {
        id: v.id || '',
        icon: v.slug || '',
        title: v.title || '',
        isMatched: false /* IsMatched will be implemented accroding to 'search filters' */,
      };
    }),
  };

  const {
    setFalse: handleCloseBookModal,
    setTrue: handleOpenBookModal,
    value: isBookFormModalOpen,
  } = useBoolean(false);

  const { handleShare, disabled: isShareButtonDisabled } = useShareController({
    address: resultData.address || '',
    zip: resultData.zip || '',
    link: url,
    description: get(resultData.description, DESCRIPTION_SLUGS['summary'], ''),
    imgSrc: gallery[0]?.src || null,
    imgName: gallery[0]?.title || '',
  });

  const aside = useMemo(() => {
    return (
      <Box sx={{ ml: 3, width: 342 }}>
        <BlockWrapper variant="secondary">
          <BookInfoForm
            rating={resultData.rating}
            reviewsCount={resultData.reviewsCount}
            basePrice={resultData.basePrice}
            discount={resultData.discount}
            maxAccommodatesCount={accommodatesValue}
            listingId={resultData.id}
            listingSlug={resultData.slug}
          />
        </BlockWrapper>
        {/* Comented by PM request 19.04.2023 */}
        {/* <BlockWrapper variant="secondary" sx={{ mt: 2 }}>
          {contactAgentSectionElement}
        </BlockWrapper> */}
      </Box>
    );
  }, [
    accommodatesValue,
    resultData.basePrice,
    resultData.discount,
    resultData.id,
    resultData.rating,
    resultData.reviewsCount,
    resultData.slug,
  ]);

  const defaultDividerSx: SxProps = {
    my: { xs: 3, sm: 3, md: 6, xl: 6, lg: 6 },
  };

  return (
    <StyledListingPage>
      <BookInfoModal
        open={isBookFormModalOpen}
        onOpen={handleOpenBookModal}
        onClose={handleCloseBookModal}
        title={translate('form.title')}
        fullScreen
        formProps={{
          rating: resultData.rating,
          reviewsCount: resultData.reviewsCount,
          basePrice: resultData.basePrice,
          discount: resultData.discount,
          listingId: resultData.id,
          maxAccommodatesCount: accommodatesValue || 1,
          listingSlug: resultData.slug,
          onCloseModal: handleCloseBookModal,
        }}
      />
      <Container disableGutters={isMobile}>
        <ListingGalleryView images={resultData.gallery} />
      </Container>
      <Container>
        <Box>
          {/* Render Anchor menu only in desktop version */}
          <Box className={ListingPageClasses.anchorMenuWrapper}>
            <AnchorMenu />
          </Box>

          <WithStickySectionWrapper
            stickyElement={aside}
            sxStickyElement={{ top: HEADER_HEIGHT + 24, display: { xs: 'none', md: 'block' } }}
            sx={{ mt: { xs: 3, md: 6 } }}
          >
            {/* Main title */}
            <Typography variant="h3" bigVariant="h1" sx={{ wordBreak: 'break-word' }}>
              {resultData.title}
            </Typography>
            <Box className={ListingPageClasses.mainInfoSection}>
              <Box>
                {resultData.address ? (
                  <LocationView label={resultData.address} sx={{ mt: 1 }} />
                ) : null}
                <AmenitiesStackView items={resultData.mainAmenities} sx={{ mt: 1 }} />
              </Box>
              <Box className={ListingPageClasses.mainInfoSectionActions}>
                {/* Like/Dislike feature:
                <Box sx={{ display: 'flex' }}>
                <ReactionButton reactionType="like" sx={{ mr: 1 }} />
                <ReactionButton reactionType="dislike" sx={{ mr: 1 }} />
                </Box> */}
                {resultData.tourLink ? (
                  <Button
                    startIcon={<IcoMoon icon="3d-tour" />}
                    href={resultData.tourLink}
                    target="_blank"
                    component={Link}
                  >
                    <Typography component="span" variant="buttonUnderline">
                      {translate.root('buttons.3d-tour')}
                    </Typography>
                  </Button>
                ) : null}
                {!isShareButtonDisabled && (
                  <Button startIcon={<IcoMoon icon="share" />} onClick={handleShare}>
                    <Typography component="span" variant="buttonUnderline">
                      {translate.root('custom.share')}
                    </Typography>
                  </Button>
                )}
              </Box>
            </Box>
            <Divider sx={defaultDividerSx} />
            {/* ----------------------------------------------
                              Description section
                ---------------------------------------------- */}
            <AnchorSection id="description" sx={{ mb: { xs: 3, sm: 7 } }}>
              <Typography variant="h4">{translate('titles.description')}</Typography>
              {!isEmpty(resultData.description) ? (
                <Box mt={3}>
                  <DescriptionSection description={resultData.description} />
                </Box>
              ) : (
                <Typography variant="subtitle1" sx={{ mt: 4 }}>
                  {translate.root('custom.no_description')}
                </Typography>
              )}
            </AnchorSection>
            {/* ----------------------------------------------
                              Amenities section
                ---------------------------------------------- */}
            <AnchorSection id="amenities" sx={{ mb: { xs: 3, sm: 7 } }}>
              <AmenitiesSection
                items={resultData.amenities}
                title={translate('titles.amenities')}
              />
            </AnchorSection>

            <PoiContextProvider>
              {/* ----------------------------------------------
                              POI section
                ---------------------------------------------- */}
              <Box className={ListingPageClasses.POISection}>
                <ClosestPOISwiper listingId={resultData.id} />
              </Box>

              {/* ----------------------------------------------
                              Map section
                ---------------------------------------------- */}
              <AnchorSection id="map" sx={{ mb: { xs: 3, sm: 8 } }}>
                <ListingMap listingId={id} location={resultData.location} />
              </AnchorSection>
            </PoiContextProvider>

            {/* ----------------------------------------------
                              Reviews section
                ---------------------------------------------- */}
            <AnchorSection id="review">
              <ListingReviewsSection
                id={resultData.id}
                averageRating={resultData.rating}
                reviewsCount={resultData.reviewsCount}
                maxReviews={4}
              />
            </AnchorSection>
            {/* ----------------------------------------------
                  House rules and cancellation policy section
                ---------------------------------------------- */}
            <AnchorSection id="requirements">
              <BlockWrapper sx={{ mt: 6 }}>
                <HouseRulesSection requirements={resultData.houseRules} />
              </BlockWrapper>
              <BlockWrapper sx={{ mt: 3 }}>
                <CancellationPolicySection policyData={resultData.cancelationPolicyInfo} />
              </BlockWrapper>
            </AnchorSection>
            {/* Feature <ListingReactionSectionView sx={{ mt: 6 }} /> */}
          </WithStickySectionWrapper>

          {/* ----------------------------------------------
                        You may also like section
              ---------------------------------------------- */}
          {!!resultData.similarListings.length && (
            <>
              <Divider sx={defaultDividerSx} />
              <RecommendedListingsSection recommendedListings={resultData.similarListings} />
            </>
          )}
        </Box>
      </Container>
      {isMobile ? (
        <Box className={ListingPageClasses.fixedMobileBookSection}>
          <Box>
            {resultData.basePrice ? (
              <PriceView prefix="from" variant="per_night" price={resultData.basePrice} />
            ) : null}
          </Box>
          <Button variant="contained" onClick={handleOpenBookModal}>
            {translate(isSelectedDates ? 'form.book_now' : 'form.check_availability')}
          </Button>
        </Box>
      ) : null}
    </StyledListingPage>
  );
}

export default ListingPage;
