import { differenceInCalendarDays } from 'date-fns';
import { max, min } from 'lodash';

import { AvailabilityData } from '../../types';

interface GetClosestDateParams {
  date: Date;
  data: AvailabilityData;
  position: 'before' | 'after';
  filter: (item: AvailabilityData[number]) => boolean;
}

type GetClosestDateReturn = Date | null;

export const getClosestDate = (params: GetClosestDateParams): GetClosestDateReturn => {
  const { data, date: referenceDate, position, filter } = params;

  return data.reduce<GetClosestDateReturn>((prev, item) => {
    const isMatchFilter = filter(item);
    if (!isMatchFilter) return prev;

    const daysDifference = differenceInCalendarDays(referenceDate, item.date);

    const isDateBeforeReferenceDate = daysDifference > 0;
    const isDateAfterReferenceDate = daysDifference < 0;

    const shouldBeBefore = position === 'before';
    const isMatchPosition = shouldBeBefore ? isDateBeforeReferenceDate : isDateAfterReferenceDate;

    if (!isMatchPosition) return prev;

    if (prev) {
      const result = shouldBeBefore ? max([prev, item.date]) : min([prev, item.date]);
      return result ?? null;
    }

    return item.date;
  }, null);
};
