import { Children, FC, useCallback, useState } from 'react';

import { Box, IconButton, IconButtonProps, Theme, useMediaQuery } from '@mui/material';
import { clsx } from 'clsx';
import { IcoMoon } from 'custom/IcoMoon';
import SwiperCore from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';
import { Swiper, SwiperProps, SwiperSlide } from 'swiper/react';
import { styled } from 'theme/index';

import 'swiper/css/pagination';

const PREFIX = 'GallerySwiper';

export const GallerySwiperClasses = {
  navigationButtonWrapper: `${PREFIX}-navigationButtonWrapper`,
  prevButtonWrapper: `${PREFIX}-prevButtonWrapper`,
  nextButtonWrapper: `${PREFIX}-nextButtonWrapper`,
  prevButton: `${PREFIX}-prevButton`,
  nextButton: `${PREFIX}-nextButton`,

  // swiper core and modules classes, add others if need
  swiper: 'swiper',
  swiperSlide: 'swiper-slide',
};

export const StyledRoot = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const isRtl = theme.direction === 'rtl';
  const mobile = theme.breakpoints.down('md');

  return {
    position: 'relative',
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    padding: `0 ${theme.spacing(3)}`,
    [mobile]: {
      padding: 0,
    },

    [`& .${GallerySwiperClasses.swiper}`]: {
      width: '100%',
      height: '100%',
      margin: theme.spacing(0, 3),
      paddingBottom: theme.spacing(6),
      [mobile]: {
        margin: 0,
      },
    },

    [`& .${GallerySwiperClasses.swiperSlide}`]: {
      height: '100%',
    },

    [`& .${GallerySwiperClasses.navigationButtonWrapper}`]: {
      position: 'absolute',
      top: '50%',
      transform: 'translateY(-50%)',
      zIndex: 2,
    },
    [`& .${GallerySwiperClasses.prevButtonWrapper}`]: {
      left: theme.spacing(1),
    },
    [`& .${GallerySwiperClasses.nextButtonWrapper}`]: {
      right: theme.spacing(1),
    },

    [`& .${GallerySwiperClasses.prevButton}`]: {
      transform: isRtl ? 'rotate(180deg)' : 'rotate(0deg)',
    },

    [`& .${GallerySwiperClasses.nextButton}`]: {
      transform: isRtl ? 'rotate(0deg)' : 'rotate(180deg)',
    },
  };
});

const ArrowButton = (props: Omit<IconButtonProps, 'children'>) => {
  const { className, ...rest } = props;

  return (
    <IconButton size="small" {...rest}>
      <IcoMoon icon="chevron-left" size="small" className={className} />
    </IconButton>
  );
};

export const GallerySwiper: FC<SwiperProps> = (props) => {
  const { pagination, children, ...restSwiperProps } = props || {};

  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const [prevElement, setPrevElement] = useState<HTMLDivElement | null>();
  const [nextElement, setNextElement] = useState<HTMLDivElement | null>();

  const handleBeforeInit = useCallback(
    (swiper: SwiperCore) => {
      const navigation = swiper.params.navigation;

      if (navigation && typeof navigation !== 'boolean') {
        navigation.prevEl = prevElement;
        navigation.nextEl = nextElement;
      }
    },
    [nextElement, prevElement],
  );

  return (
    <StyledRoot>
      {isMobile ? null : (
        <Box
          ref={setPrevElement}
          className={clsx(
            GallerySwiperClasses.navigationButtonWrapper,
            GallerySwiperClasses.prevButtonWrapper,
          )}
        >
          <ArrowButton className={GallerySwiperClasses.prevButton} />
        </Box>
      )}

      <Swiper
        className={GallerySwiperClasses.swiper}
        modules={[Navigation, Pagination]}
        loop={true}
        navigation={{
          prevEl: prevElement,
          nextEl: nextElement,
        }}
        onBeforeInit={handleBeforeInit}
        pagination={{ type: 'fraction' }}
        {...restSwiperProps}
      >
        {Children.map(children, (child, index) => {
          return <SwiperSlide key={index}>{child}</SwiperSlide>;
        })}
      </Swiper>

      {isMobile ? null : (
        <Box
          ref={setNextElement}
          className={clsx(
            GallerySwiperClasses.navigationButtonWrapper,
            GallerySwiperClasses.nextButtonWrapper,
          )}
        >
          <ArrowButton className={GallerySwiperClasses.nextButton} />
        </Box>
      )}
    </StyledRoot>
  );
};
