import { FC } from 'react';

import { Box, BoxProps } from '@mui/material';
import { GiftIcon } from '@packages/assets';

import { Typography } from '../../Typography';

export interface LowerPriceViewProps extends BoxProps {
  discount: number;
  title?: string; // set default as 'New lower price' using translations
  subtitle?: string; // set default as 'Price your trip was...' using translations with passing custom option (%55)
}

export const LowerPriceView: FC<LowerPriceViewProps> = (props) => {
  const {
    title = 'components.lower_price.title',
    subtitle = 'components.lower_price.subtitle',
    discount,
    sx,
    ...restBoxProps
  } = props;

  return (
    <Box
      sx={{
        borderColor: 'primary.border',
        backgroundColor: 'background.default',
        borderRadius: 4,
        p: 2,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        ...sx,
      }}
      border={1}
      {...restBoxProps}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'start',
        }}
      >
        <Typography variant="subtitle2">{title}</Typography>
        <Typography
          variant="body1"
          color="text.light"
          sx={{ mr: 3 }}
          translateOptions={{ value: discount }}
        >
          {subtitle}
        </Typography>
      </Box>
      <GiftIcon />
    </Box>
  );
};

export default LowerPriceView;
