import { FC } from 'react';

import { Box, Typography } from '@mui/material';
import { useEffectOnValueChange } from '@packages/helpers';
import { Identifier } from '@packages/types';
import { useGetListingPOI, usePoiContext } from 'components/poi';
import { DefaultPOIItem, DefaultPOIItemProps } from 'components/poi/views';
import { useTranslate } from 'hooks/useTranslate';
import { Pagination } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
import { styled } from 'theme/index';

import 'swiper/css/pagination';

const PREFIX = 'ClosestPOISwiper';

export const ClosestPOISwiperClasses = {
  // swiper core and modules classes, add others if need
  swiperSlide: 'swiper-slide',
  paginationBullet: 'swiper-pagination-bullet',
  paginationActiveBullet: 'swiper-pagination-bullet-active',
};

export const StyledSwiper = styled(Swiper, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    paddingBottom: 48,
    [`&& .${ClosestPOISwiperClasses.swiperSlide}`]: {
      width: 205,
    },
    [`&& .${ClosestPOISwiperClasses.paginationBullet}`]: {
      width: 8,
      height: 8,
      border: '1px solid',
      borderColor: theme.palette.primary.main,
      backgroundColor: 'transparent',
    },
    [`&& .${ClosestPOISwiperClasses.paginationActiveBullet}`]: {
      backgroundColor: theme.palette.primary.main,
    },
  };
});

export interface ClosestPOISwiperProps {
  listingId: Identifier;
}
export const ClosestPOISwiper: FC<ClosestPOISwiperProps> = (props) => {
  const { listingId } = props;
  const { data: poiData = [] } = useGetListingPOI({ listingId });
  const { setListingPoiIds, setPoisById, activeId, setActiveId } = usePoiContext();
  const t = useTranslate({ basePath: 'pages.listing' });

  useEffectOnValueChange({
    value: poiData,
    onChange: () => {
      setListingPoiIds(poiData.map((v) => v.id));
      setPoisById((prev) => {
        const newPoisById = poiData.reduce((acc, v) => {
          return {
            ...acc,
            [v.id]: {
              ...v,
              address: v.title,
            },
          } as typeof prev;
        }, prev);

        return newPoisById;
      });
    },
    callOnInitialisation: true,
  });

  if (!poiData.length) return null;

  return (
    <Box>
      <Typography variant="h4" sx={{ mb: 4 }}>
        {t('titles.poi')}
      </Typography>

      <StyledSwiper
        slidesPerView="auto"
        spaceBetween={12}
        modules={[Pagination]}
        pagination={{
          clickable: true,
          dynamicBullets: true,
          dynamicMainBullets: 3,
        }}
      >
        {poiData?.map((poi, index) => {
          const {
            id,
            title,
            image,
            walk_meters,
            drive_seconds = 0,
            bicycle_seconds = 0,
            walk_seconds = 0,
          } = poi;

          const travelMinutes: DefaultPOIItemProps['travelMinutes'] = {
            car: drive_seconds / 60,
            bicycle: bicycle_seconds / 60,
            walk: walk_seconds / 60,
          };
          return (
            <SwiperSlide key={index} className={ClosestPOISwiperClasses.swiperSlide}>
              <DefaultPOIItem
                title={title}
                src={image?.src}
                meters={walk_meters}
                travelMinutes={travelMinutes}
                isActive={activeId === id}
                onClick={() => setActiveId(activeId === id ? null : id)}
              />
            </SwiperSlide>
          );
        })}
      </StyledSwiper>
    </Box>
  );
};
