import { addMonths, endOfMonth, isAfter, isBefore, startOfDay, startOfMonth } from 'date-fns';

export interface GetAvailabilityRangeFromDateParams {
  date: Date | undefined;
  months: number;
  minDate?: Date;
  maxDate?: Date;
}

export type GetAvailabilityRangeFromDateReturn = [Date, Date];

export const getAvailabilityRangeFromDate = (
  params: GetAvailabilityRangeFromDateParams,
): GetAvailabilityRangeFromDateReturn => {
  const { minDate, date = new Date(), maxDate, months } = params;

  let startDate = startOfMonth(date);

  // startOfDay used to reset the time to 0 in order to write tests easier
  let endDate = startOfDay(endOfMonth(addMonths(startDate, months - 1)));

  if (minDate) {
    if (isBefore(startDate, minDate)) {
      startDate = minDate;
    }
    if (isBefore(endDate, minDate)) {
      endDate = minDate;
    }
  }

  if (maxDate) {
    if (isAfter(startDate, maxDate)) {
      startDate = maxDate;
    }
    if (isAfter(endDate, maxDate)) {
      endDate = maxDate;
    }
  }

  return [startDate, endDate];
};
