import { zodIdSchema } from '@packages/providers';
import { TypesFromSchemas } from '@packages/types';
import { z } from 'zod';

const zodPlacesAutocompleteSchema = z
  .object({
    id: zodIdSchema,
  })
  .merge(
    z
      .object({
        description: z.string(),
      })
      .deepPartial(),
  );

export const zodPlacesAutocompleteSchemas = {
  record: zodPlacesAutocompleteSchema,
  base: z.array(zodPlacesAutocompleteSchema).default([]),
};

export type PlacesAutocompleteTypes = TypesFromSchemas<typeof zodPlacesAutocompleteSchemas>;
