import { useMemo } from 'react';

import { fetchClient, zodSearchSchemas } from '@packages/providers';
import { ListFilterType } from '@packages/types';
import { QueryFunction, useQuery } from '@tanstack/react-query';
import {
  BASE_PRICE_SLUG,
  COUNTRY_SOURCE,
  PURPOSE_TYPES,
  STATE_SOURCE,
} from 'application/constants';
import { SEARCH_RESOURCE } from 'application/resources';
import { useListContext } from 'contexts/ListContext';
import { getParsedFilters } from 'helpers/filters/getParsedFilters';
import { get, isEmpty } from 'lodash';
import { z } from 'zod';

import { useGetFilterAttributes } from '../useGetFilterAttributes';

type FilterType = Record<string, unknown>;

interface UseGetRecommendedListingsParams {
  filter: ListFilterType;
}

export type RecommendedListings = z.infer<typeof zodSearchSchemas.index>;

const fetchListings: QueryFunction<RecommendedListings, [FilterType]> = async (context) => {
  const { queryKey } = context;
  const [filter] = queryKey;

  const {
    data: { data: recommendedListings },
  } = await fetchClient.get({
    pathname: SEARCH_RESOURCE,
    filter,
    sort: { field: 'rating', order: 'DESC' },
  });

  return zodSearchSchemas.index.parse(recommendedListings);
};

export const useGetRecommendedListings = (params: UseGetRecommendedListingsParams) => {
  const { filter: listingFilter } = params;

  const { listParams } = useListContext();

  const { data: filterAttributes = [] } = useGetFilterAttributes({
    purpose: PURPOSE_TYPES['searching'],
  });

  /**
   * We need to prepare filter to allow send single values
   * in Array if FA 'is_multiple: true'
   */
  const { selectedFilters } = getParsedFilters({
    filter: listParams?.filter,
    filterAttributes: filterAttributes,
    independentFiltersSlugs: [COUNTRY_SOURCE, STATE_SOURCE],
  });

  const excludeIds = get(listingFilter, 'exclude_ids');

  const finalFilter = useMemo(() => {
    // if local storage filter is not empty then use it, otherwise, use listing data for filtering
    const filter = isEmpty(selectedFilters)
      ? listingFilter
      : { exclude_ids: excludeIds, ...selectedFilters };

    const { city, country, ...restFilter } = filter;

    const isCountryCityFilters = !!city && !!country;

    return {
      ...restFilter,
      ...(isCountryCityFilters && {
        city,
        country,
      }),
      state_code: selectedFilters.state,
      country_code: selectedFilters.country,
      [BASE_PRICE_SLUG]: {
        min: get(filter, [BASE_PRICE_SLUG, 0]),
        max: get(filter, [BASE_PRICE_SLUG, 1]),
      },
    };
  }, [excludeIds, listingFilter, selectedFilters]);

  return useQuery({
    queryKey: [finalFilter],
    queryFn: fetchListings,
    enabled: !isEmpty(filterAttributes),
  });
};
