import { FC, ReactNode } from 'react';

import { Box } from '@mui/material';
import { StyledRootProps } from '@packages/types';

import { styled } from '../../../theme';

const PREFIX = 'WithActionsWrapper';

export const WithActionsWrapperClasses = {
  actions: `${PREFIX}-actions`,
};

const StyledWithActionsWrapper = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`& .${WithActionsWrapperClasses.actions}`]: {
      position: 'absolute',
      padding: theme.spacing(2),
    },
  };
});

export interface WithActionsWrapperProps extends StyledRootProps {
  children: ReactNode;
  actions: ReactNode;
  variant?: 'topRight' | 'bottomRight';
}

export const WithActionsWrapper: FC<WithActionsWrapperProps> = (props) => {
  const { actions, children, className, sx, variant = 'topRight' } = props;

  return (
    <StyledWithActionsWrapper
      className={className}
      sx={{ position: 'relative', height: '100%', width: '100%', ...sx }}
    >
      <Box
        className={WithActionsWrapperClasses.actions}
        sx={{
          '&&': {
            ...(variant === 'topRight' ? { top: 0, right: 0 } : {}),
            ...(variant === 'bottomRight' ? { bottom: 0, right: 0 } : {}),
          },
        }}
      >
        {actions}
      </Box>
      {children}
    </StyledWithActionsWrapper>
  );
};
