import { FC } from 'react';

import { styled } from 'theme/index';
import {
  DateRangePicker,
  DateRangePickerClasses,
  DateRangePickerProps,
} from 'views/DateRangePickerInputView';

import { PickerDay, PickerDayClasses } from '../PickerDay';

const PREFIX = 'BookingDateRangePicker';

const StyledDateRangePicker = styled(DateRangePicker, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`& .${DateRangePickerClasses.dayDisabled}`]: {
      [`& .${PickerDayClasses.inactive}`]: {
        textDecoration: 'none',
        color: theme.palette.text.secondary,
      },
    },
  };
});

type PickerProps = DateRangePickerProps<typeof PickerDay>;

export const Picker: FC<PickerProps> = (props) => {
  const { slots, ...rest } = props;

  return <StyledDateRangePicker {...rest} slots={{ ...slots, day: PickerDay }} />;
};
